import { TidsperiodeMedValgfriSluttdato, TidsperiodeMedValgfriSluttdatoDate } from '@navikt/fp-common';

export enum Nringstype {
    FISKER = 'FISKE',
    JORDBRUK = 'JORDBRUK_SKOGBRUK',
    DAGMAMMA = 'DAGMAMMA',
    ANNET = 'ANNEN',
}

export interface Nring {
    nringstyper: Nringstype[];
    tidsperiode: TidsperiodeMedValgfriSluttdato;
    nringsinntekt?: string;
    pgende: boolean;
    navnPNringen: string;
    organisasjonsnummer?: string;
    registrertINorge: boolean;
    registrertILand?: string;
    harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene?: boolean;
    oppstartsdato?: string;
    hattVarigEndringAvNringsinntektSiste4Kalenderr?: boolean;
    endringAvNringsinntektInformasjon?: EndringAvNringsinntektInformasjon;
    harRegnskapsfrer: boolean;
    regnskapsfrer?: Nringsrelasjon;
}

export interface NringInnsending
    extends Omit<Nring, 'tidsperiode' | 'endringAvNringsinntektInformasjon' | 'oppstartsdato' | 'nringsinntekt'> {
    nringsinntekt: number;
    tidsperiode: TidsperiodeMedValgfriSluttdatoDate;
    oppstartsdato?: Date;
    endringAvNringsinntektInformasjon?: EndringAvNringsinntektInformasjonInnsending;
}

export interface EndringAvNringsinntektInformasjon {
    dato: string;
    nringsinntektEtterEndring: number;
    forklaring: string;
}

export interface EndringAvNringsinntektInformasjonInnsending extends Omit<EndringAvNringsinntektInformasjon, 'dato'> {
    dato: Date;
}

export interface Nringsrelasjon {
    navn: string;
    telefonnummer: string;
    erNrVennEllerFamilie: boolean;
}
