import { hasValue } from '@navikt/fp-common';
import Skersituasjon from 'app/context/types/Skersituasjon';
import { SkersituasjonFormData } from './skersituasjonFormConfig';

export const mapSkersituasjonFormDataToState = (formData: Partial<SkersituasjonFormData>): Skersituasjon => {
    return {
        situasjon: formData.situasjon!,
        rolle: hasValue(formData.rolle) ? formData.rolle! : 'far',
    };
};
