import React, { useEffect, useState } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import DocumentTitle from 'react-document-title';
import Lenke from 'nav-frontend-lenker';
import { Ingress, Normaltekst } from 'nav-frontend-typografi';

import KvitteringHeader from './components/KvitteringHeader';
import KvitteringSuksess from './components/KvitteringSuksess';
import StatusBoks from './components/StatusBoks';
import SknadSendtSectionHeader from './components/SknadSendtSectionHeader';
import { Kvittering } from 'app/types/Kvittering';
import { bemUtils, Block, intlUtils } from '@navikt/fp-common';
import { openPdfPreview } from 'app/utils/pdfUtils';
import links from 'app/links/links';
import { logAmplitudeEvent, PageKeys } from 'app/amplitude/amplitude';
import Api from 'app/api/api';
import dayjs from 'dayjs';
import NavFrontendSpinner from 'nav-frontend-spinner';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import { MissingAttachment } from 'app/types/MissingAttachment';

import './sknadSendt.less';
import SknadSendtTittel from './components/SknadSendtTittel';

const SknadSendt = () => {
    const [kvittering, setKvittering] = useState<Kvittering>();
    const skerinfo = useSkerinfo();
    const missingAttachments: MissingAttachment[] = [];
    const erEndringssknad = false;
    const behandlingsFrist = '2021-06-14';
    const intl = useIntl();
    const { person, arbeidsforhold } = skerinfo;
    const bem = bemUtils('sknadSendt');

    useEffect(() => {
        Api.sendStorageKvittering({ innsendingstidspunkt: dayjs().format('YYYY-MM-DD') }, skerinfo.person.fnr).then(
            (response) => setKvittering(response.data)
        );

        logAmplitudeEvent('sidevisning', {
            app: 'foreldrepengesoknad',
            team: 'foreldrepenger',
            pageKey: PageKeys.SknadSendt,
        });
    }, [skerinfo.person.fnr]);

    if (!kvittering) {
        return (
            <div style={{ textAlign: 'center', padding: '12rem 0' }}>
                <NavFrontendSpinner type="XXL" />
            </div>
        );
    }

    return (
        <>
            <DocumentTitle title={intlUtils(intl, 'sknad.sknadSendt')} />
            <SknadSendtTittel />
            <div className={bem.block}>
                <KvitteringHeader sker={person} kvittering={kvittering} />

                <Block padBottom="l">
                    <KvitteringSuksess missingAttachments={missingAttachments} />
                </Block>

                {!erEndringssknad && (
                    <>
                        <Block padBottom="l">
                            <SknadSendtSectionHeader
                                title={intlUtils(intl, 'sknadSendt.nr.tittel')}
                                type="kalender"
                                info={intlUtils(intl, 'sknadSendt.nr.infoBox')}
                                infoApneLabel={intlUtils(intl, 'sknadSendt.nr.infoBox.apneLabel')}
                            >
                                <Normaltekst>{dayjs(behandlingsFrist).format('D. MMMM YYYY')}</Normaltekst>
                            </SknadSendtSectionHeader>
                        </Block>

                        {arbeidsforhold && arbeidsforhold.length > 0 && (
                            <Block padBottom="l">
                                <SknadSendtSectionHeader
                                    title={intlUtils(intl, 'sknadSendt.infoFraArbeidsgiver.tittel')}
                                    type="koffert"
                                    info={intlUtils(intl, 'sknadSendt.infoFraArbeidsgiver.infoBox')}
                                    infoApneLabel={intlUtils(intl, 'sknadSendt.infoFraArbeidsgiver.infoBox.apneLabel')}
                                >
                                    <Block padBottom="l">
                                        <Normaltekst>
                                            <FormattedMessage id={'sknadSendt.infoFraArbeidsgiver.del1'} />
                                        </Normaltekst>
                                    </Block>
                                    <Normaltekst>
                                        <Lenke
                                            href={'#'}
                                            onClick={(e) => {
                                                e.preventDefault();
                                                openPdfPreview(kvittering.infoskrivPdf);
                                            }}
                                        >
                                            <FormattedMessage id={'sknadSendt.infoFraArbeidsgiver.del2'} />
                                        </Lenke>
                                    </Normaltekst>
                                </SknadSendtSectionHeader>
                            </Block>
                        )}

                        <Block padBottom="l">
                            <SknadSendtSectionHeader
                                title={intlUtils(intl, 'sknadSendt.pengene.tittel')}
                                type="cash"
                                info={intlUtils(intl, 'sknadSendt.pengene.infoBox')}
                                infoApneLabel={intlUtils(intl, 'sknadSendt.pengene.infoBox.apneLabel')}
                            >
                                {person.bankkonto && person.bankkonto.kontonummer ? (
                                    <>
                                        <Block padBottom="l">
                                            <Normaltekst>
                                                <FormattedMessage id="sknadSendt.pengene.kontonummer" />
                                            </Normaltekst>
                                        </Block>
                                        <Block padBottom="l">
                                            <Ingress>{person.bankkonto && person.bankkonto.kontonummer}</Ingress>
                                        </Block>
                                        <Block padBottom="l">
                                            <Normaltekst>
                                                <Lenke href={links.brukerprofil}>
                                                    <FormattedMessage id="sknadSendt.pengene.kontonummer.endre" />
                                                </Lenke>
                                            </Normaltekst>
                                        </Block>
                                    </>
                                ) : (
                                    <>
                                        <Block padBottom="l">
                                            <Normaltekst>
                                                <FormattedMessage id="sknadSendt.pengene.ingenKontonummer" />
                                            </Normaltekst>
                                        </Block>
                                        <Block padBottom="l">
                                            <Normaltekst>
                                                <Lenke href={links.brukerprofil}>
                                                    <FormattedMessage id="sknadSendt.pengene.kontonummer.leggTil" />
                                                </Lenke>
                                            </Normaltekst>
                                        </Block>
                                    </>
                                )}
                            </SknadSendtSectionHeader>
                        </Block>
                    </>
                )}

                <StatusBoks saksNr={kvittering.saksNr} />
            </div>
        </>
    );
};

export default SknadSendt;
