import SknadRoutes from 'app/routes/routes';
import { Attachment } from 'app/types/Attachment';
import { Dekningsgrad } from 'app/types/Dekningsgrad';
import Sak from 'app/types/Sak';
import { Skerinfo } from 'app/types/Skerinfo';
import { ForeldrepengesknadContextState } from '../ForeldrepengesknadContextConfig';
import AnnenForelder from '../types/AnnenForelder';
import Barn from '../types/Barn';
import InformasjonOmUtenlandsopphold from '../types/InformasjonOmUtenlandsopphold';
import Sker from '../types/Sker';
import Skersituasjon from '../types/Skersituasjon';
import UttaksplanInfo from '../types/UttaksplanInfo';

export enum ForeldrepengesknadContextActionKeys {
    SET_HARGODKJENTVILKR = 'setVelkommen',
    SET_SKERSITUASJON = 'setSkersituasjon',
    SET_OMBARNET = 'setOmBarnet',
    AVBRYT_SKNAD = 'avbrytSknad',
    UPDATE_CURRENT_ROUTE = 'updateCurrentRoute',
    APPLY_STORED_STATE = 'applyStoredState',
    SET_ANNENFORELDER = 'setAnnenForelder',
    SET_SKERINFO = 'setSkerinfo',
    SET_SKER = 'setSker',
    SET_INFORMASJON_OM_UTENLANDSOPPHOLD = 'setInformasjonOmUtenlandsopphold',
    SET_INFORMASJON_OM_ANDRE_INNTEKTER = 'setInformasjonOmAndreInntekter',
    SET_SAKER = 'setSaker',
    SET_UTTAKSPLAN_INFO = 'setUttaksplanInfo',
    SET_DEKNINGSGRAD = 'setDekningsgrad',
    SET_VEDLEGG = 'setVedlegg',
}

interface SetVelkommen {
    type: ForeldrepengesknadContextActionKeys.SET_HARGODKJENTVILKR;
    payload: boolean;
}

const setVelkommen = (payload: boolean): SetVelkommen => ({
    type: ForeldrepengesknadContextActionKeys.SET_HARGODKJENTVILKR,
    payload,
});

interface SetSkersituasjon {
    type: ForeldrepengesknadContextActionKeys.SET_SKERSITUASJON;
    payload: Skersituasjon;
}

const setSkersituasjon = (payload: Skersituasjon): SetSkersituasjon => ({
    type: ForeldrepengesknadContextActionKeys.SET_SKERSITUASJON,
    payload,
});

interface SetOmBarnet {
    type: ForeldrepengesknadContextActionKeys.SET_OMBARNET;
    payload: Barn;
}

const setOmBarnet = (payload: Barn): SetOmBarnet => ({
    type: ForeldrepengesknadContextActionKeys.SET_OMBARNET,
    payload,
});

interface AvbrytSknad {
    type: ForeldrepengesknadContextActionKeys.AVBRYT_SKNAD;
}

const avbrytSknad = (): AvbrytSknad => ({
    type: ForeldrepengesknadContextActionKeys.AVBRYT_SKNAD,
});

interface UpdateCurrentRoute {
    type: ForeldrepengesknadContextActionKeys.UPDATE_CURRENT_ROUTE;
    payload: SknadRoutes;
}

const updateCurrentRoute = (payload: SknadRoutes): UpdateCurrentRoute => ({
    type: ForeldrepengesknadContextActionKeys.UPDATE_CURRENT_ROUTE,
    payload,
});

interface ApplyStoredState {
    type: ForeldrepengesknadContextActionKeys.APPLY_STORED_STATE;
    payload: ForeldrepengesknadContextState;
}

const applyStoredState = (payload: ForeldrepengesknadContextState): ApplyStoredState => ({
    type: ForeldrepengesknadContextActionKeys.APPLY_STORED_STATE,
    payload,
});

interface SetAnnenForelder {
    type: ForeldrepengesknadContextActionKeys.SET_ANNENFORELDER;
    payload: AnnenForelder;
}

const setAnnenForelder = (payload: AnnenForelder): SetAnnenForelder => ({
    type: ForeldrepengesknadContextActionKeys.SET_ANNENFORELDER,
    payload,
});

interface SetSkerinfo {
    type: ForeldrepengesknadContextActionKeys.SET_SKERINFO;
    payload: Skerinfo;
}

const setSkerinfo = (payload: Skerinfo): SetSkerinfo => ({
    type: ForeldrepengesknadContextActionKeys.SET_SKERINFO,
    payload,
});

interface SetSker {
    type: ForeldrepengesknadContextActionKeys.SET_SKER;
    payload: Sker;
}

const setSker = (payload: Sker): SetSker => ({
    type: ForeldrepengesknadContextActionKeys.SET_SKER,
    payload,
});

interface SetInformasjonOmUtenlandsopphold {
    type: ForeldrepengesknadContextActionKeys.SET_INFORMASJON_OM_UTENLANDSOPPHOLD;
    payload: InformasjonOmUtenlandsopphold;
}

const setInformasjonOmUtenlandsopphold = (
    payload: InformasjonOmUtenlandsopphold
): SetInformasjonOmUtenlandsopphold => ({
    type: ForeldrepengesknadContextActionKeys.SET_INFORMASJON_OM_UTENLANDSOPPHOLD,
    payload,
});

interface SetSaker {
    type: ForeldrepengesknadContextActionKeys.SET_SAKER;
    payload: Sak[];
}

const setSaker = (payload: Sak[]): SetSaker => ({
    type: ForeldrepengesknadContextActionKeys.SET_SAKER,
    payload,
});

interface SetUttaksplanInfo {
    type: ForeldrepengesknadContextActionKeys.SET_UTTAKSPLAN_INFO;
    payload: UttaksplanInfo;
}

const setUttaksplanInfo = (payload: UttaksplanInfo): SetUttaksplanInfo => ({
    type: ForeldrepengesknadContextActionKeys.SET_UTTAKSPLAN_INFO,
    payload,
});

interface SetDekningsgrad {
    type: ForeldrepengesknadContextActionKeys.SET_DEKNINGSGRAD;
    dekningsgrad: Dekningsgrad;
}

const setDekningsgrad = (dekningsgrad: Dekningsgrad): SetDekningsgrad => ({
    type: ForeldrepengesknadContextActionKeys.SET_DEKNINGSGRAD,
    dekningsgrad,
});

interface SetVedlegg {
    type: ForeldrepengesknadContextActionKeys.SET_VEDLEGG;
    vedlegg: Attachment[];
}

const setVedlegg = (vedlegg: Attachment[]): SetVedlegg => ({
    type: ForeldrepengesknadContextActionKeys.SET_VEDLEGG,
    vedlegg,
});

export type ForeldrepengesknadContextAction =
    | SetVelkommen
    | SetSkersituasjon
    | SetOmBarnet
    | AvbrytSknad
    | UpdateCurrentRoute
    | SetAnnenForelder
    | SetSkerinfo
    | SetSker
    | SetInformasjonOmUtenlandsopphold
    | SetSaker
    | SetUttaksplanInfo
    | SetDekningsgrad
    | ApplyStoredState
    | SetVedlegg;

export default {
    setVelkommen,
    setSkersituasjon,
    setOmBarnet,
    avbrytSknad,
    updateCurrentRoute,
    applyStoredState,
    setAnnenForelder,
    setSkerinfo,
    setSker,
    setInformasjonOmUtenlandsopphold,
    setSaker,
    setUttaksplanInfo,
    setDekningsgrad,
    setVedlegg,
};
