import { TidsperiodeMedValgfriSluttdato } from '@navikt/fp-common';
import { Attachment } from 'app/types/Attachment';

export enum AnnenInntektType {
    SLUTTPAKKE = 'ETTERLNN_SLUTTPAKKE',
    MILITRTJENESTE = 'MILITR_ELLER_SIVILTJENESTE',
    VENTELNN = 'VENTELNN_VARTPENGER',
    JOBB_I_UTLANDET = 'JOBB_I_UTLANDET',
}

interface AnnenInntektBase {
    type: AnnenInntektType;
    tidsperiode: TidsperiodeMedValgfriSluttdato;
    pgende: boolean;
    vedlegg: Attachment[];
}

export interface SluttpakkeInntekt extends AnnenInntektBase {
    type: AnnenInntektType.SLUTTPAKKE;
}

export interface MilitrtjenesteInntekt extends AnnenInntektBase {
    type: AnnenInntektType.MILITRTJENESTE;
}

export interface JobbIUtlandetInntekt extends AnnenInntektBase {
    type: AnnenInntektType.JOBB_I_UTLANDET;
    arbeidsgiverNavn: string;
    land: string;
}

export interface VentelnnInntekt extends AnnenInntektBase {
    type: AnnenInntektType.VENTELNN;
}

export type AnnenInntekt = SluttpakkeInntekt | MilitrtjenesteInntekt | JobbIUtlandetInntekt | VentelnnInntekt;

export interface AnnenInntektBaseInnsending extends Omit<AnnenInntektBase, 'tidsperiode'> {
    tidsperiode: TidsperiodeMedValgfriSluttdato;
}

export interface SluttpakkeInntektInnsending extends AnnenInntektBaseInnsending {
    type: AnnenInntektType.SLUTTPAKKE;
}

export interface MilitrtjenesteInntektInnsending extends AnnenInntektBaseInnsending {
    type: AnnenInntektType.MILITRTJENESTE;
}

export interface JobbIUtlandetInntektInnsending extends AnnenInntektBaseInnsending {
    type: AnnenInntektType.JOBB_I_UTLANDET;
    arbeidsgiverNavn: string;
    land: string;
}

export interface VentelnnInntektInnsending extends AnnenInntektBaseInnsending {
    type: AnnenInntektType.VENTELNN;
}

export type AnnenInntektInnsending =
    | SluttpakkeInntektInnsending
    | MilitrtjenesteInntektInnsending
    | JobbIUtlandetInntektInnsending
    | VentelnnInntektInnsending;
