import { useHistory } from 'react-router';
import { useForeldrepengesknadContext } from 'app/context/hooks/useForeldrepengesknadContext';
import { logAmplitudeEvent } from 'app/amplitude/amplitude';
import Api from 'app/api/api';
import actionCreator from 'app/context/action/actionCreator';
import { useCallback } from 'react';

const useAvbrytSknad = () => {
    const history = useHistory();
    const { dispatch } = useForeldrepengesknadContext();

    const avbrytSknadHandler = useCallback(() => {
        logAmplitudeEvent('applikasjon-hendelse', {
            app: 'foreldrepengesoknad',
            team: 'foreldrepenger',
            hendelse: 'avbrutt',
        });

        dispatch(actionCreator.avbrytSknad());
        Api.deleteStoredAppState();
        history.push('/');
    }, [history, dispatch]);

    return avbrytSknadHandler;
};

export default useAvbrytSknad;
