const getSkerFromSaksgrunnlag = (grunnlag: Saksgrunnlag, erFarEllerMedmor: boolean): Partial<Sker> => {
    return {
        erAleneOmOmsorg: erFarEllerMedmor ? grunnlag.farMedmorErAleneOmOmsorg : grunnlag.morErAleneOmOmsorg,
    };
};

const getBarnFromSaksgrunnlag = (situasjon: Situasjon, sak: Saksgrunnlag, skerinfo: Skerinfo): Barn | undefined => {
    const nyesteBarn = skerinfo.registrerteBarn.sort((a, b) =>
        dayjs(b.fdselsdato).format('YYYY-MM-DD').localeCompare(dayjs(a.fdselsdato).format('YYYY-MM-DD'))
    )[0];

    let erBarnetFdt;
    if (nyesteBarn === undefined) {
        erBarnetFdt = sak.erBarnetFdt;
    } else {
        erBarnetFdt = dayjs(nyesteBarn.fdselsdato).isBetween(
            dayjs(sak.familiehendelseDato).subtract(20, 'weeks'),
            dayjs(sak.familiehendelseDato).add(6, 'weeks')
        );
    }

    if (erBarnetFdt === undefined) {
        return undefined;
    }

    switch (situasjon) {
        case 'fdsel':
            if (sak.fdselsdato) {
                return {
                    type: BarnType.FDT,
                    antallBarn: sak.antallBarn.toString(),
                    fdselsdatoer: [sak.familiehendelseDato],
                    termindato: sak.termindato,
                };
            }

            return {
                type: BarnType.UFDT,
                antallBarn: sak.antallBarn.toString(),
                termindato: sak.familiehendelseDato,
                terminbekreftelse: [],
            };
        case 'adopsjon':
            return {
                type: BarnType.ADOPTERT_STEBARN,
                adopsjonsdato: sak.familiehendelseDato,
                antallBarn: sak.antallBarn.toString(),
                fdselsdatoer: [sak.familiehendelseDato],
                omsorgsovertakelse: [],
            };
        default:
            return undefined;
    }
};

const getAnnenForelderFromSaksgrunnlag = (
    situasjon: Situasjon,
    grunnlag: Saksgrunnlag,
    annenPart: AnnenPart,
    erFarEllerMedmor: boolean
): AnnenForelder | undefined => {
    switch (situasjon) {
        case 'fdsel':
        case 'adopsjon':
            if (erFarEllerMedmor) {
                return {
                    fornavn: annenPart.navn.fornavn,
                    etternavn: annenPart.navn.etternavn,
                    erUfr: grunnlag.morErUfr,
                    harRettPForeldrepenger: grunnlag.morHarRett,
                    fnr: annenPart.fnr,
                    kanIkkeOppgis: false,
                };
            }
            return {
                fornavn: annenPart.navn.fornavn,
                etternavn: annenPart.navn.etternavn,
                harRettPForeldrepenger: grunnlag.farMedmorHarRett,
                fnr: annenPart.fnr,
                kanIkkeOppgis: false,
            };
        default:
            return undefined;
    }
};

const kanSaksperiodeKonverteresTilPeriode = (periode: Saksperiode) => {
    if (periode.flerbarnsdager === false) {
        return true;
    }
    return false;
};

export const kanUttaksplanGjennskapesFraSak = (perioder: Saksperiode[]): boolean => {
    const noenPerioderKanIkkeGjennskapes = perioder.some(
        (periode) => kanSaksperiodeKonverteresTilPeriode(periode) === false
    );

    return noenPerioderKanIkkeGjennskapes === false;
};

const finnAnnenForelderPFdselsdato = (
    barn: RegistrertBarn[],
    fdselsdato: Date | undefined,
    grunnlag: Saksgrunnlag,
    situasjon: Situasjon,
    erFarEllerMedmor: boolean
): AnnenForelder | undefined => {
    if (fdselsdato === undefined) {
        return undefined;
    }

    const barnMedGittFdselsdato = barn.find((b) => dayjs(b.fdselsdato).isSame(dayjs(fdselsdato)));

    if (barnMedGittFdselsdato !== undefined) {
        const annenForelder = barnMedGittFdselsdato.annenForelder;

        if (annenForelder !== undefined) {
            const { fnr, etternavn, fornavn } = annenForelder;
            const annenPart: Partial<AnnenPart> = {
                navn: {
                    fornavn,
                    etternavn,
                },
                fnr,
            };
            return getAnnenForelderFromSaksgrunnlag(situasjon, grunnlag, annenPart as AnnenPart, erFarEllerMedmor);
        }
    }
};

