import { bemUtils } from '@navikt/fp-common';
import KvinneIkon from 'app/assets/KvinneIkon';
import { Skerinfo } from 'app/types/Skerinfo';
import { Normaltekst, Undertittel } from 'nav-frontend-typografi';
import React, { FunctionComponent } from 'react';

import './personalia.less';

interface Props {
    skerinfo: Skerinfo;
}

const Personalia: FunctionComponent<Props> = ({ skerinfo }) => {
    const bem = bemUtils('personalia');

    return (
        <div className={bem.block}>
            <div className={bem.element('icon')}>
                <KvinneIkon />
            </div>
            <div>
                <Undertittel>{`${skerinfo.person.fornavn} ${skerinfo.person.etternavn}`}</Undertittel>
                <Normaltekst>{skerinfo.person.fnr}</Normaltekst>
            </div>
        </div>
    );
};

export default Personalia;
