import dayjs from 'dayjs';
const isValidFdselsnummer = require('is-valid-fodselsnummer');

type FdselsnummerValidationResult = false | 'F' | 'D';
export const isFdselsnummerFormatValid = (fnr: string): FdselsnummerValidationResult => {
    try {
        return isValidFdselsnummer(fnr, true);
    } catch (e) {
        return false;
    }
};

export const isSixteenOrOlder = (fnr: string, isFdselsnummerValid: FdselsnummerValidationResult): boolean => {
    const dato = isFdselsnummerValid === 'D' ? `${Number(fnr.substr(0, 1)) - 4}${fnr.substr(1, 1)}` : fnr.substr(0, 2);
    const mnd = fnr.substr(2, 2);
    const r = fnr.substr(4, 2);

    const fdselsdato = dayjs(`${dato}-${mnd}-${r}`, 'DD-MM-YY');

    if (fdselsdato.get('year') > dayjs().get('year')) {
        fdselsdato.subtract(100, 'year');
    }

    if (!fdselsdato.isValid()) {
        return false;
    }

    return fdselsdato.isBefore(dayjs().subtract(16, 'year'));
};
