import { Block, intlUtils } from '@navikt/fp-common';
import { YesOrNo } from '@navikt/sif-common-formik/lib';
import { QuestionVisibility } from '@navikt/sif-common-question-config/lib';
import Skersituasjon from 'app/context/types/Skersituasjon';
import dayjs from 'dayjs';
import { FieldArray } from 'formik';
import React, { FunctionComponent } from 'react';
import { useIntl } from 'react-intl';
import { OmBarnetFormComponents, OmBarnetFormData, OmBarnetFormField } from '../omBarnetFormConfig';
import { validateFdselsdato, validateTermindatoFdsel } from '../validation/omBarnetValidering';

interface Props {
    skersituasjon: Skersituasjon;
    formValues: OmBarnetFormData;
    visibility: QuestionVisibility<OmBarnetFormField, undefined>;
}

const Fdsel: FunctionComponent<Props> = ({ skersituasjon, formValues, visibility }) => {
    const { erBarnetFdt } = formValues;

    const intl = useIntl();

    if (skersituasjon.situasjon === 'adopsjon' || erBarnetFdt !== YesOrNo.YES) {
        return null;
    }

    return (
        <>
            <Block padBottom="l" visible={visibility.isVisible(OmBarnetFormField.antallBarn)}>
                <OmBarnetFormComponents.RadioPanelGroup
                    name={OmBarnetFormField.antallBarn}
                    radios={[
                        {
                            label: intlUtils(intl, 'omBarnet.radiobutton.ettBarn'),
                            value: '1',
                        },
                        {
                            label: intlUtils(intl, 'omBarnet.radiobutton.tvillinger'),
                            value: '2',
                        },
                        {
                            label: intlUtils(intl, 'omBarnet.radiobutton.flere'),
                            value: '3',
                        },
                    ]}
                    useTwoColumns={true}
                    legend={intlUtils(intl, 'omBarnet.antallBarn.fdt')}
                />
            </Block>
            <Block
                padBottom="l"
                visible={formValues.antallBarn !== undefined && parseInt(formValues.antallBarn, 10) >= 3}
            >
                <OmBarnetFormComponents.Select name={OmBarnetFormField.antallBarn}>
                    <option value="3">3</option>
                    <option value="4">4</option>
                    <option value="5">5</option>
                    <option value="6">6</option>
                    <option value="7">7</option>
                    <option value="8">8</option>
                    <option value="9">9</option>
                </OmBarnetFormComponents.Select>
            </Block>
            <Block padBottom="l" visible={visibility.isVisible(OmBarnetFormField.fdselsdatoer)}>
                <FieldArray
                    name={OmBarnetFormField.fdselsdatoer}
                    render={() => [
                        <OmBarnetFormComponents.DatePicker
                            key={`${OmBarnetFormField.fdselsdatoer}.0`}
                            name={`${OmBarnetFormField.fdselsdatoer}.0` as OmBarnetFormField}
                            label={intlUtils(intl, 'omBarnet.fdselsdato')}
                            minDate={dayjs().subtract(6, 'months').toDate()}
                            maxDate={dayjs().toDate()}
                            validate={validateFdselsdato(intl)}
                            placeholder={'dd.mm.'}
                        />,
                    ]}
                />
            </Block>
            <Block visible={visibility.isVisible(OmBarnetFormField.termindato)}>
                <OmBarnetFormComponents.DatePicker
                    name={OmBarnetFormField.termindato}
                    label={intlUtils(intl, 'omBarnet.termindato.fdt')}
                    minDate={dayjs().subtract(6, 'months').toDate()}
                    maxDate={dayjs().add(9, 'months').toDate()}
                    placeholder={'dd.mm.'}
                    validate={validateTermindatoFdsel(intl)}
                />
            </Block>
        </>
    );
};

export default Fdsel;
