import { FarMedmorFdselOgMorHarIkkeRettUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import { Dekningsgrad } from 'app/types/Dekningsgrad';
import {
    FarMedmorFdselOgMorHarIkkeRettFormData,
    FarMedmorFdselOgMorHarIkkeRettFormField,
} from './farMedmorFdselOgMorHarIkkeRettFormConfig';

const initialFarMedmorFdselOgMorHarIkkeRettValues: FarMedmorFdselOgMorHarIkkeRettFormData = {
    [FarMedmorFdselOgMorHarIkkeRettFormField.dekningsgrad]: '',
    [FarMedmorFdselOgMorHarIkkeRettFormField.permisjonStartdato]: '',
};

export const mapFarMedmorFdselOgMorHarIkkeRettFormToState = (
    values: Partial<FarMedmorFdselOgMorHarIkkeRettFormData>
): FarMedmorFdselOgMorHarIkkeRettUttaksplanInfo => {
    return {
        permisjonStartdato: values.permisjonStartdato!,
    };
};

export const getInitialFarMedmorFdselOgMorHarIkkeRettValues = (
    dekningsgrad: Dekningsgrad,
    lagretUttaksplanInfo: FarMedmorFdselOgMorHarIkkeRettUttaksplanInfo | undefined
): FarMedmorFdselOgMorHarIkkeRettFormData => {
    if (lagretUttaksplanInfo) {
        return {
            ...lagretUttaksplanInfo,
            [FarMedmorFdselOgMorHarIkkeRettFormField.dekningsgrad]: dekningsgrad,
        };
    }

    return initialFarMedmorFdselOgMorHarIkkeRettValues;
};
