import { QuestionConfig, Questions } from '@navikt/sif-common-question-config';
import { hasValue } from '@navikt/fp-common';
import { MorFdselFormData, MorFdselFormField } from './morFdselFormConfig';

interface MorFdselQuestionsPayload extends MorFdselFormData {
    harRettPForeldrepenger: boolean | undefined;
    erAleneOmOmsorg: boolean | undefined;
}

const MorFdselFormConfig: QuestionConfig<MorFdselQuestionsPayload, MorFdselFormField> = {
    [MorFdselFormField.dekningsgrad]: {
        isAnswered: ({ dekningsgrad }) => hasValue(dekningsgrad),
        isIncluded: () => true,
    },
    [MorFdselFormField.permisjonStartdato]: {
        isAnswered: ({ permisjonStartdato }) => hasValue(permisjonStartdato),
        isIncluded: () => true,
        visibilityFilter: ({ dekningsgrad }) => hasValue(dekningsgrad),
    },
    [MorFdselFormField.skalIkkeHaUttakFrTermin]: {
        isAnswered: ({ skalIkkeHaUttakFrTermin }) => hasValue(skalIkkeHaUttakFrTermin),
        isIncluded: () => true,
        visibilityFilter: ({ dekningsgrad }) => hasValue(dekningsgrad),
    },
    [MorFdselFormField.fellesperiodeukerMor]: {
        isAnswered: ({ fellesperiodeukerMor }) => hasValue(fellesperiodeukerMor),
        isIncluded: ({ harRettPForeldrepenger, erAleneOmOmsorg }) =>
            !!harRettPForeldrepenger && erAleneOmOmsorg === false,
        visibilityFilter: ({ dekningsgrad, permisjonStartdato, skalIkkeHaUttakFrTermin }) =>
            hasValue(dekningsgrad) && (hasValue(permisjonStartdato) || skalIkkeHaUttakFrTermin === true),
    },
};

export const morFdselQuestionsConfig = Questions<MorFdselQuestionsPayload, MorFdselFormField>(MorFdselFormConfig);
