import React, { FunctionComponent } from 'react';
import { IntlShape, useIntl } from 'react-intl';
import dayjs from 'dayjs';
import { Block, intlUtils, bemUtils } from '@navikt/fp-common';
import { ISOStringToDate } from '@navikt/sif-common-formik/lib';
import { MorFdselFormComponents, MorFdselFormField } from './morFdselFormConfig';
import useSknad from 'app/utils/hooks/useSknad';
import { isFdtBarn } from 'app/context/types/Barn';
import { Tidsperioden, getValidTidsperiode } from 'app/steps/uttaksplan-info/utils/Tidsperioden';
import uttaksConstants from 'app/constants';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import { getFamiliehendelsedato } from 'app/utils/barnUtils';
import { validateErStartdatoFrTermindato } from './validation/morFodselValidering';
import VeilederStartdatoPermisjon from './VeilederStartdatoPermisjon';
import { uttaksplanDatoavgrensninger } from 'app/steps/uttaksplan-info/utils/uttaksplanDatoavgrensninger';
import { getVarighetString } from 'app/utils/dateUtils';

import './startdatoPermisjonMor.less';

const getVarighetForStartdato = (antallDager: number, barnetErFdt: boolean, intl: IntlShape): string | undefined =>
    antallDager > 0
        ? barnetErFdt
            ? intlUtils(intl, 'uttaksplaninfo.sprsml.startdatoPermisjon.barnetErFdt.varighet', {
                  varighet: getVarighetString(antallDager, intl),
              })
            : intlUtils(intl, 'uttaksplaninfo.sprsml.startdatoPermisjon.varighet', {
                  varighet: getVarighetString(antallDager, intl),
              })
        : undefined;

interface Props {
    permisjonStartdato: string;
    skalIkkeHaUttakFrTermin: boolean;
}

const StartdatoPermisjonMor: FunctionComponent<Props> = ({ permisjonStartdato, skalIkkeHaUttakFrTermin }) => {
    const intl = useIntl();
    const { barn } = useSknad();
    const bem = bemUtils('datoInput');

    const erBarnFdt = isFdtBarn(barn);
    const sprsmlNr = erBarnFdt
        ? intlUtils(intl, 'uttaksplaninfo.sprsml.startdatoPermisjon.barnetErFdt.label')
        : intlUtils(intl, 'uttaksplaninfo.sprsml.startdatoPermisjon.label');

    const sprsmlHaddeIkke = erBarnFdt
        ? intlUtils(intl, 'uttaksplaninfo.sprsml.startdatoPermisjon.skalIkkeHaUttak.barnetErFdt.label')
        : intlUtils(intl, 'uttaksplaninfo.sprsml.startdatoPermisjon.skalIkkeHaUttak.label');

    const familiehendelsesdato = getFamiliehendelsedato(barn);
    const familiehendelsesdatoDate = dayjs(familiehendelsesdato).toDate();
    const sisteUttaksdagFrTermin = Uttaksdagen(familiehendelsesdatoDate).forrige();
    const tidsperiode = getValidTidsperiode({
        fom: ISOStringToDate(permisjonStartdato)!,
        tom: sisteUttaksdagFrTermin,
    });
    const antallDager = tidsperiode ? Tidsperioden(tidsperiode).getAntallUttaksdager() : 0;
    const antallDagerFrFdselIhtRegler = uttaksConstants.ANTALL_UKER_FORELDREPENGER_FR_FDSEL * 5;
    const visVeileder = antallDager !== antallDagerFrFdselIhtRegler;

    const datoAvgrensninger = uttaksplanDatoavgrensninger.startdatoFrTermin(familiehendelsesdato);
    const startdato = skalIkkeHaUttakFrTermin !== true ? permisjonStartdato : undefined;

    const maksDato = Uttaksdagen(familiehendelsesdatoDate).forrige();

    return (
        <>
            <Block padBottom="l">
                <div className={bem.block}>
                    <MorFdselFormComponents.DatePicker
                        name={MorFdselFormField.permisjonStartdato}
                        label={sprsmlNr}
                        disabled={skalIkkeHaUttakFrTermin}
                        maxDate={maksDato}
                        dayPickerProps={{
                            initialMonth: ISOStringToDate(permisjonStartdato) || familiehendelsesdatoDate,
                        }}
                        validate={validateErStartdatoFrTermindato(
                            intl,
                            familiehendelsesdatoDate,
                            skalIkkeHaUttakFrTermin
                        )}
                        disableWeekend
                    />
                    <div className={bem.element('postfix')}>
                        {getVarighetForStartdato(antallDager, erBarnFdt, intl)}
                    </div>
                </div>
            </Block>
            <Block padBottom={visVeileder ? 'l' : 'm'}>
                <MorFdselFormComponents.Checkbox
                    name={MorFdselFormField.skalIkkeHaUttakFrTermin}
                    label={sprsmlHaddeIkke}
                    autoComplete="off"
                />
            </Block>
            <Block padBottom={visVeileder ? 'l' : 'm'} visible={visVeileder}>
                <VeilederStartdatoPermisjon
                    startdato={ISOStringToDate(startdato)}
                    antallDager={antallDager}
                    skalIkkeHaUttakFrTermin={skalIkkeHaUttakFrTermin === true}
                    antallDagerFrFdselIhtRegler={antallDagerFrFdselIhtRegler}
                    frsteMuligeStartdato={datoAvgrensninger.minDate ? new Date(datoAvgrensninger.minDate) : undefined}
                />
            </Block>
        </>
    );
};

export default StartdatoPermisjonMor;
