import { Block, formatDate, intlUtils } from '@navikt/fp-common';
import { ISOStringToDate } from '@navikt/sif-common-formik/lib';
import useSknad from 'app/utils/hooks/useSknad';
import { Normaltekst } from 'nav-frontend-typografi';
import React, { FunctionComponent } from 'react';
import { useIntl } from 'react-intl';
import OppsummeringsPunkt from '../OppsummeringsPunkt';
import InntekterTabell from './InntekterTabell';

const FrilansOppsummering: FunctionComponent = () => {
    const intl = useIntl();
    const {
        sker: { frilansInformasjon, harJobbetSomFrilansSiste10Mnd },
    } = useSknad();

    if (!frilansInformasjon || !harJobbetSomFrilansSiste10Mnd) {
        return (
            <OppsummeringsPunkt title={intlUtils(intl, 'oppsummering.frilans.tittel')}>
                <Normaltekst>{intlUtils(intl, 'oppsummering.frilans.ikkeFrilans')}</Normaltekst>
            </OppsummeringsPunkt>
        );
    }
    const {
        oppstart,
        jobberFremdelesSomFrilans,
        driverFosterhjem,
        harJobbetForNrVennEllerFamilieSiste10Mnd,
        oppdragForNreVennerEllerFamilieSiste10Mnd,
    } = frilansInformasjon;

    return (
        <>
            <OppsummeringsPunkt title={intlUtils(intl, 'oppsummering.frilans.tittel')} />
            <OppsummeringsPunkt title={intlUtils(intl, 'oppsummering.frilans.oppstartsdato')}>
                <Normaltekst>{formatDate(ISOStringToDate(oppstart)!)}</Normaltekst>
            </OppsummeringsPunkt>
            <OppsummeringsPunkt title={intlUtils(intl, 'oppsummering.frilans.fremdelesFrilans')}>
                <Normaltekst>{jobberFremdelesSomFrilans ? intlUtils(intl, 'ja') : intlUtils(intl, 'nei')}</Normaltekst>
            </OppsummeringsPunkt>
            <OppsummeringsPunkt title={intlUtils(intl, 'oppsummering.frilans.driverFosterhjem')}>
                <Normaltekst>{driverFosterhjem ? intlUtils(intl, 'ja') : intlUtils(intl, 'nei')}</Normaltekst>
            </OppsummeringsPunkt>
            <OppsummeringsPunkt
                title={intlUtils(intl, 'oppsummering.frilans.frilansArbeidForNreVennerEllerFamilieSiste10Mnd')}
            >
                <Block visible={!harJobbetForNrVennEllerFamilieSiste10Mnd} margin="none">
                    <Normaltekst>
                        {intlUtils(
                            intl,
                            'oppsummering.frilans.harIkkeUtfrtFrilansArbeidForNreVennerEllerFamilieSiste10Mnd'
                        )}
                    </Normaltekst>
                </Block>
                <Block visible={harJobbetForNrVennEllerFamilieSiste10Mnd} margin="none">
                    <InntekterTabell
                        list={oppdragForNreVennerEllerFamilieSiste10Mnd.map(
                            ({ navnPArbeidsgiver, tidsperiode, pgende }) => ({
                                key: navnPArbeidsgiver + tidsperiode,
                                headerVenstre: navnPArbeidsgiver,
                                headerHyre: intlUtils(intl, 'tidsintervall', {
                                    fom: formatDate(ISOStringToDate(tidsperiode.fom)!),
                                    tom: pgende ? 'pgende' : formatDate(ISOStringToDate(tidsperiode.tom)!),
                                }),
                            })
                        )}
                    />
                </Block>
            </OppsummeringsPunkt>
        </>
    );
};

export default FrilansOppsummering;
