import { hasValue } from '@navikt/fp-common';
import { YesOrNo } from '@navikt/sif-common-formik/lib';
import { QuestionVisibility } from '@navikt/sif-common-question-config/lib';
import { Nring, EndringAvNringsinntektInformasjon, Nringsrelasjon } from 'app/context/types/Nring';
import { date4YearsAgo } from 'app/utils/dateUtils';
import { convertBooleanOrUndefinedToYesOrNo, convertYesOrNoOrUndefinedToBoolean } from 'app/utils/formUtils';
import dayjs from 'dayjs';
import { EgenNringModalFormData, EgenNringModalFormField } from './egenNringModalFormConfig';

export const initialEgenNringModalValues: EgenNringModalFormData = {
    [EgenNringModalFormField.typer]: [],
    [EgenNringModalFormField.navnPNringen]: '',
    [EgenNringModalFormField.registrertINorge]: YesOrNo.UNANSWERED,
    [EgenNringModalFormField.orgnr]: '',
    [EgenNringModalFormField.land]: '',
    [EgenNringModalFormField.tom]: '',
    [EgenNringModalFormField.fom]: '',
    [EgenNringModalFormField.pgende]: YesOrNo.UNANSWERED,
    [EgenNringModalFormField.nringsresultat]: '',
    [EgenNringModalFormField.hattVarigEndringAvNringsinntektSiste4Kalenderr]: YesOrNo.UNANSWERED,
    [EgenNringModalFormField.harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene]: YesOrNo.UNANSWERED,
    [EgenNringModalFormField.yrkesAktivDato]: '',
    [EgenNringModalFormField.datoForEndring]: '',
    [EgenNringModalFormField.inntektEtterEndring]: '',
    [EgenNringModalFormField.forklaringEndring]: '',
    [EgenNringModalFormField.harRegnskapsfrer]: YesOrNo.UNANSWERED,
    [EgenNringModalFormField.navnRegnskapsfrer]: '',
    [EgenNringModalFormField.telefonRegnskapsfrer]: '',
    [EgenNringModalFormField.regnskapsfrerNrVennEllerFamilie]: YesOrNo.UNANSWERED,
};

export const cleanupEgenNringForm = (
    values: EgenNringModalFormData,
    visibility: QuestionVisibility<EgenNringModalFormField, undefined>
): EgenNringModalFormData => {
    return {
        typer: visibility.isVisible(EgenNringModalFormField.typer) ? values.typer : [],
        navnPNringen: visibility.isVisible(EgenNringModalFormField.navnPNringen)
            ? values.navnPNringen
            : initialEgenNringModalValues.navnPNringen,
        registrertINorge: visibility.isVisible(EgenNringModalFormField.registrertINorge)
            ? values.registrertINorge
            : initialEgenNringModalValues.registrertINorge,
        orgnr: visibility.isVisible(EgenNringModalFormField.orgnr) ? values.orgnr : initialEgenNringModalValues.orgnr,
        land: visibility.isVisible(EgenNringModalFormField.land) ? values.land : initialEgenNringModalValues.land,
        tom: visibility.isVisible(EgenNringModalFormField.tom) ? values.tom : initialEgenNringModalValues.tom,
        fom: visibility.isVisible(EgenNringModalFormField.fom) ? values.fom : initialEgenNringModalValues.fom,
        pgende: visibility.isVisible(EgenNringModalFormField.pgende)
            ? values.pgende
            : initialEgenNringModalValues.pgende,
        nringsresultat: visibility.isVisible(EgenNringModalFormField.nringsresultat)
            ? values.nringsresultat
            : initialEgenNringModalValues.nringsresultat,
        hattVarigEndringAvNringsinntektSiste4Kalenderr: visibility.isVisible(
            EgenNringModalFormField.hattVarigEndringAvNringsinntektSiste4Kalenderr
        )
            ? values.hattVarigEndringAvNringsinntektSiste4Kalenderr
            : initialEgenNringModalValues.hattVarigEndringAvNringsinntektSiste4Kalenderr,
        harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene: visibility.isVisible(
            EgenNringModalFormField.harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene
        )
            ? values.harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene
            : initialEgenNringModalValues.harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene,
        yrkesAktivDato: visibility.isVisible(EgenNringModalFormField.yrkesAktivDato)
            ? values.yrkesAktivDato
            : initialEgenNringModalValues.yrkesAktivDato,
        datoForEndring: visibility.isVisible(EgenNringModalFormField.datoForEndring)
            ? values.datoForEndring
            : initialEgenNringModalValues.datoForEndring,
        inntektEtterEndring: visibility.isVisible(EgenNringModalFormField.inntektEtterEndring)
            ? values.inntektEtterEndring
            : initialEgenNringModalValues.inntektEtterEndring,
        forklaringEndring: visibility.isVisible(EgenNringModalFormField.forklaringEndring)
            ? values.forklaringEndring
            : initialEgenNringModalValues.forklaringEndring,
        harRegnskapsfrer: visibility.isVisible(EgenNringModalFormField.harRegnskapsfrer)
            ? values.harRegnskapsfrer
            : initialEgenNringModalValues.harRegnskapsfrer,
        navnRegnskapsfrer: visibility.isVisible(EgenNringModalFormField.navnRegnskapsfrer)
            ? values.navnRegnskapsfrer
            : initialEgenNringModalValues.navnRegnskapsfrer,
        telefonRegnskapsfrer: visibility.isVisible(EgenNringModalFormField.telefonRegnskapsfrer)
            ? values.telefonRegnskapsfrer
            : initialEgenNringModalValues.telefonRegnskapsfrer,
        regnskapsfrerNrVennEllerFamilie: visibility.isVisible(
            EgenNringModalFormField.regnskapsfrerNrVennEllerFamilie
        )
            ? values.regnskapsfrerNrVennEllerFamilie
            : initialEgenNringModalValues.regnskapsfrerNrVennEllerFamilie,
    };
};

export const getInitialEgenNringModalValues = (nring: Nring | undefined): EgenNringModalFormData => {
    if (!nring) {
        return {
            ...initialEgenNringModalValues,
        };
    }

    return {
        ...initialEgenNringModalValues,
        typer: nring.nringstyper,
        navnPNringen: nring.navnPNringen,
        registrertINorge: convertBooleanOrUndefinedToYesOrNo(nring.registrertINorge),
        land: nring.registrertILand || '',
        fom: nring.tidsperiode.fom,
        tom: nring.tidsperiode.tom || '',
        orgnr: nring.organisasjonsnummer || '',
        pgende: convertBooleanOrUndefinedToYesOrNo(nring.pgende),
        nringsresultat: nring.nringsinntekt || '',
        hattVarigEndringAvNringsinntektSiste4Kalenderr: convertBooleanOrUndefinedToYesOrNo(
            nring.hattVarigEndringAvNringsinntektSiste4Kalenderr
        ),
        harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene: convertBooleanOrUndefinedToYesOrNo(
            nring.harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene
        ),
        yrkesAktivDato: nring.oppstartsdato || '',
        datoForEndring: nring.endringAvNringsinntektInformasjon ? nring.endringAvNringsinntektInformasjon.dato : '',
        inntektEtterEndring: nring.endringAvNringsinntektInformasjon
            ? nring.endringAvNringsinntektInformasjon.nringsinntektEtterEndring.toString()
            : '',
        forklaringEndring: nring.endringAvNringsinntektInformasjon
            ? nring.endringAvNringsinntektInformasjon.forklaring
            : '',
        harRegnskapsfrer: convertBooleanOrUndefinedToYesOrNo(nring.harRegnskapsfrer),
        navnRegnskapsfrer: nring.regnskapsfrer ? nring.regnskapsfrer.navn : '',
        telefonRegnskapsfrer: nring.regnskapsfrer ? nring.regnskapsfrer.telefonnummer : '',
        regnskapsfrerNrVennEllerFamilie: convertBooleanOrUndefinedToYesOrNo(
            nring.regnskapsfrer ? nring.regnskapsfrer.erNrVennEllerFamilie : undefined
        ),
    };
};

export const mapEgenNringModalFormValuesToState = (values: Partial<EgenNringModalFormData>): Nring => {
    let endringAvNringsinntektInformasjon: EndringAvNringsinntektInformasjon | undefined = undefined;
    let regnskapsfrer: Nringsrelasjon | undefined = undefined;

    if (values.hattVarigEndringAvNringsinntektSiste4Kalenderr === YesOrNo.YES) {
        endringAvNringsinntektInformasjon = {
            dato: values.datoForEndring!,
            forklaring: values.forklaringEndring!,
            nringsinntektEtterEndring: parseInt(values.inntektEtterEndring!),
        };
    }

    if (values.harRegnskapsfrer === YesOrNo.YES) {
        regnskapsfrer = {
            navn: values.navnRegnskapsfrer!,
            telefonnummer: values.telefonRegnskapsfrer!,
            erNrVennEllerFamilie: convertYesOrNoOrUndefinedToBoolean(values.regnskapsfrerNrVennEllerFamilie)!,
        };
    }

    return {
        nringstyper: values.typer!,
        navnPNringen: values.navnPNringen!,
        registrertINorge: convertYesOrNoOrUndefinedToBoolean(values.registrertINorge)!,
        organisasjonsnummer: hasValue(values.orgnr) ? values.orgnr : undefined,
        registrertILand: hasValue(values.land) ? values.land : undefined,
        tidsperiode: {
            fom: values.fom!,
            tom: values.tom,
        },
        pgende: convertYesOrNoOrUndefinedToBoolean(values.pgende)!,
        nringsinntekt: hasValue(values.nringsresultat) ? values.nringsresultat : undefined,
        hattVarigEndringAvNringsinntektSiste4Kalenderr: convertYesOrNoOrUndefinedToBoolean(
            values.hattVarigEndringAvNringsinntektSiste4Kalenderr
        ),
        harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene: convertYesOrNoOrUndefinedToBoolean(
            values.harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene
        ),
        oppstartsdato: hasValue(values.yrkesAktivDato) ? values.yrkesAktivDato : undefined,
        endringAvNringsinntektInformasjon: endringAvNringsinntektInformasjon,
        harRegnskapsfrer: convertYesOrNoOrUndefinedToBoolean(values.harRegnskapsfrer)!,
        regnskapsfrer: regnskapsfrer,
    };
};

export const erVirksomhetRegnetSomNyoppstartet = (oppstartsdato: Date | undefined): boolean => {
    if (!oppstartsdato) {
        return true;
    }

    return dayjs(oppstartsdato).startOf('day').isAfter(date4YearsAgo);
};
