import { formatDate } from '@navikt/fp-common';
import InteractiveListElement from 'app/components/interactive-list-element/InteractiveListElement';
import { Nring } from 'app/context/types/Nring';
import React, { FunctionComponent } from 'react';

interface Props {
    nringsInformasjon: Nring[];
    deleteNring: (oppdrag: Nring) => void;
    selectNring: (oppdrag: Nring) => void;
}

const EgenNringListe: FunctionComponent<Props> = ({ nringsInformasjon, deleteNring, selectNring }) => {
    if (nringsInformasjon.length === 0) {
        return null;
    }

    return (
        <ul style={{ margin: 0, padding: 0, listStyle: 'none' }}>
            {nringsInformasjon.map((nring, index) => (
                <InteractiveListElement
                    deleteLinkText="Slett nring"
                    onDelete={() => deleteNring(nring)}
                    onEdit={() => selectNring(nring)}
                    text={`${formatDate(nring.tidsperiode.fom)} - ${
                        nring.tidsperiode.tom ? formatDate(nring.tidsperiode.tom) : 'pgende'
                    }`}
                    title={nring.navnPNringen}
                    deleteButtonAriaText={`Slett nring`}
                    editButtonAriaText={`Rediger nring`}
                    key={`${nring.navnPNringen}${index}`}
                />
            ))}
        </ul>
    );
};

export default EgenNringListe;
