import { formatDate, intlUtils } from '@navikt/fp-common';
import { ISOStringToDate } from '@navikt/sif-common-formik/lib';
import { Nring } from 'app/context/types/Nring';
import * as countries from 'i18n-iso-countries';
import { Normaltekst } from 'nav-frontend-typografi';
import React, { FunctionComponent } from 'react';
import { useIntl } from 'react-intl';
import OppsummeringsPunkt from '../OppsummeringsPunkt';

interface Props {
    nring: Nring;
}

const Nringsdetaljer: FunctionComponent<Props> = ({ nring }) => {
    const intl = useIntl();
    const {
        nringstyper,
        organisasjonsnummer,
        nringsinntekt,
        registrertINorge,
        registrertILand,
        harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene,
        hattVarigEndringAvNringsinntektSiste4Kalenderr,
        endringAvNringsinntektInformasjon,
        harRegnskapsfrer,
        regnskapsfrer,
    } = nring;

    return (
        <>
            <OppsummeringsPunkt title={intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.nringstype')}>
                <Normaltekst>
                    {nringstyper
                        .map((nringstype) => intlUtils(intl, `nringstype.${nringstype.toLowerCase()}`))
                        .join(', ')}
                </Normaltekst>
            </OppsummeringsPunkt>
            {organisasjonsnummer && (
                <OppsummeringsPunkt title={intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.orgnr')}>
                    <Normaltekst>{organisasjonsnummer}</Normaltekst>
                </OppsummeringsPunkt>
            )}
            {nringsinntekt && (
                <OppsummeringsPunkt title={intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.nringsinntekt')}>
                    <Normaltekst>{nringsinntekt}</Normaltekst>
                </OppsummeringsPunkt>
            )}
            {registrertINorge ||
                (registrertILand && (
                    <OppsummeringsPunkt
                        title={intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.registrertLand')}
                    >
                        <Normaltekst>
                            {registrertINorge ? 'Norge' : countries.getName(registrertILand, 'nb')}
                        </Normaltekst>
                    </OppsummeringsPunkt>
                ))}
            {harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene && (
                <OppsummeringsPunkt
                    title={intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.blittYrkesaktivSiste3r')}
                >
                    <Normaltekst>
                        {hattVarigEndringAvNringsinntektSiste4Kalenderr
                            ? intlUtils(intl, 'ja')
                            : intlUtils(intl, 'nei')}
                    </Normaltekst>
                </OppsummeringsPunkt>
            )}
            {hattVarigEndringAvNringsinntektSiste4Kalenderr === true && (
                <>
                    <OppsummeringsPunkt
                        title={intlUtils(
                            intl,
                            'oppsummering.selvstendigNringsdrivende.datoForEndringAvNringsinntekt'
                        )}
                    >
                        <Normaltekst>
                            {formatDate(ISOStringToDate(endringAvNringsinntektInformasjon?.dato)!)}
                        </Normaltekst>
                    </OppsummeringsPunkt>
                    <OppsummeringsPunkt
                        title={intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.nringsinntektEtterEndring')}
                    >
                        <Normaltekst>{endringAvNringsinntektInformasjon!.nringsinntektEtterEndring}</Normaltekst>
                    </OppsummeringsPunkt>
                    <OppsummeringsPunkt title={intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.forklaring')}>
                        <Normaltekst>{endringAvNringsinntektInformasjon!.forklaring}</Normaltekst>
                    </OppsummeringsPunkt>
                </>
            )}
            {harRegnskapsfrer === true && regnskapsfrer && (
                <>
                    <OppsummeringsPunkt
                        title={intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.regnskapsfrerNavn')}
                    >
                        <Normaltekst>{regnskapsfrer.navn}</Normaltekst>
                    </OppsummeringsPunkt>
                    <OppsummeringsPunkt
                        title={intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.regnskapsfrerTlf')}
                    >
                        <Normaltekst>{regnskapsfrer.telefonnummer}</Normaltekst>
                    </OppsummeringsPunkt>
                    <OppsummeringsPunkt
                        title={intlUtils(
                            intl,
                            'oppsummering.selvstendigNringsdrivende.regnskapsfrerNrVennEllerFamilie'
                        )}
                    >
                        <Normaltekst>
                            {regnskapsfrer.erNrVennEllerFamilie ? intlUtils(intl, 'ja') : intlUtils(intl, 'nei')}
                        </Normaltekst>
                    </OppsummeringsPunkt>
                </>
            )}
            {harRegnskapsfrer === false && (
                <OppsummeringsPunkt title={intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.regnskapsfrer')}>
                    <Normaltekst>
                        {intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.harIkkeRegnskapsfrer')}
                    </Normaltekst>
                </OppsummeringsPunkt>
            )}
        </>
    );
};

export default Nringsdetaljer;
