import { FarMedmorFdselBeggeHarRettUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import { Dekningsgrad } from 'app/types/Dekningsgrad';
import {
    FarMedmorFdselBeggeHarRettFormData,
    FarMedmorFdselBeggeHarRettFormField,
} from './farMedmorFdselBeggeHarRettFormConfig';

const initialFarMedmorFdselBeggeHarRettValues: FarMedmorFdselBeggeHarRettFormData = {
    [FarMedmorFdselBeggeHarRettFormField.dekningsgrad]: '',
    [FarMedmorFdselBeggeHarRettFormField.morsSisteDag]: '',
    [FarMedmorFdselBeggeHarRettFormField.farMedmorsFrsteDag]: '',
    [FarMedmorFdselBeggeHarRettFormField.antallUkerFellesperiode]: '0',
    [FarMedmorFdselBeggeHarRettFormField.antallDagerFellesperiode]: '0',
};

export const mapFarMedmorFdselBeggeHarRettToState = (
    values: Partial<FarMedmorFdselBeggeHarRettFormData>
): FarMedmorFdselBeggeHarRettUttaksplanInfo => {
    return {
        antallDagerFellesperiode: values.antallDagerFellesperiode!,
        antallUkerFellesperiode: values.antallUkerFellesperiode!,
        farMedmorsFrsteDag: values.farMedmorsFrsteDag!,
        morsSisteDag: values.morsSisteDag!,
    };
};

export const getInitialFarMedmorFdselBeggeHarRettValues = (
    state: FarMedmorFdselBeggeHarRettUttaksplanInfo | undefined,
    dekningsgrad: Dekningsgrad
): FarMedmorFdselBeggeHarRettFormData => {
    if (!state) {
        return initialFarMedmorFdselBeggeHarRettValues;
    }

    return {
        ...initialFarMedmorFdselBeggeHarRettValues,
        antallDagerFellesperiode: state.antallDagerFellesperiode,
        antallUkerFellesperiode: state.antallUkerFellesperiode,
        morsSisteDag: state.morsSisteDag,
        farMedmorsFrsteDag: state.farMedmorsFrsteDag,
        dekningsgrad,
    };
};
