import { hasValue } from '@navikt/fp-common';
import { getTypedFormComponents } from '@navikt/sif-common-formik/lib';
import Skersituasjon from 'app/context/types/Skersituasjon';
import { Situasjon } from 'app/types/Situasjon';
import { Skerrolle } from 'app/types/Skerrolle';

export enum SkersituasjonFormField {
    situasjon = 'situasjon',
    rolle = 'rolle',
}

export interface SkersituasjonFormData {
    [SkersituasjonFormField.situasjon]: Situasjon;
    [SkersituasjonFormField.rolle]: Skerrolle;
}

const initialSkersituasjonValues: SkersituasjonFormData = {
    [SkersituasjonFormField.situasjon]: '',
    [SkersituasjonFormField.rolle]: '',
};

export const getInitialSkerSituasjonValues = (stateValues: Skersituasjon): SkersituasjonFormData => {
    if (hasValue(stateValues.rolle) && hasValue(stateValues.situasjon)) {
        return {
            rolle: stateValues.rolle,
            situasjon: stateValues.situasjon,
        };
    }

    return initialSkersituasjonValues;
};

export const SkersituasjonFormComponents = getTypedFormComponents<
    SkersituasjonFormField,
    SkersituasjonFormData,
    string
>();
