import { formatDate, intlUtils } from '@navikt/fp-common';
import { ISOStringToDate } from '@navikt/sif-common-formik/lib';
import useSknad from 'app/utils/hooks/useSknad';
import { Normaltekst } from 'nav-frontend-typografi';
import React, { FunctionComponent } from 'react';
import { useIntl } from 'react-intl';
import OppsummeringsPunkt from '../OppsummeringsPunkt';
import InntekterTabell from './InntekterTabell';
import Nringsdetaljer from './Nringsdetaljer';

const SelvstendigNringsdrivendeOppsummering: FunctionComponent = () => {
    const intl = useIntl();
    const {
        sker: { selvstendigNringsdrivendeInformasjon, harJobbetSomSelvstendigNringsdrivendeSiste10Mnd },
    } = useSknad();

    if (!selvstendigNringsdrivendeInformasjon || !harJobbetSomSelvstendigNringsdrivendeSiste10Mnd) {
        return (
            <OppsummeringsPunkt title={intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.tittel')}>
                <Normaltekst>
                    {intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.ikkeSelvstendigNringsdrivende')}
                </Normaltekst>
            </OppsummeringsPunkt>
        );
    }

    return (
        <OppsummeringsPunkt title={intlUtils(intl, 'oppsummering.selvstendigNringsdrivende.tittel')}>
            <InntekterTabell
                list={selvstendigNringsdrivendeInformasjon.map((nring) => ({
                    key: nring.navnPNringen + nring.tidsperiode,
                    headerVenstre: nring.navnPNringen,
                    headerHyre: intlUtils(intl, 'tidsintervall', {
                        fom: formatDate(ISOStringToDate(nring.tidsperiode.fom)!),
                        tom: nring.pgende ? 'pgende' : formatDate(ISOStringToDate(nring.tidsperiode.tom)!),
                    }),
                    content: <Nringsdetaljer nring={nring} />,
                }))}
            />
        </OppsummeringsPunkt>
    );
};

export default SelvstendigNringsdrivendeOppsummering;
