import { hasValue } from '@navikt/fp-common';
import { YesOrNo } from '@navikt/sif-common-formik/lib';
import { QuestionConfig, Questions } from '@navikt/sif-common-question-config/lib';
import { getDateFromDateString } from 'app/utils/dateUtils';
import { EgenNringModalFormData, EgenNringModalFormField } from './egenNringModalFormConfig';
import { erVirksomhetRegnetSomNyoppstartet } from './egenNringModalFormUtils';

const EgenNringModalFormConfig: QuestionConfig<EgenNringModalFormData, EgenNringModalFormField> = {
    [EgenNringModalFormField.typer]: {
        isIncluded: () => true,
        isAnswered: ({ typer }) => typer.length > 0,
    },
    [EgenNringModalFormField.navnPNringen]: {
        isIncluded: () => true,
        isAnswered: ({ navnPNringen }) => hasValue(navnPNringen),
        visibilityFilter: ({ typer }) => typer.length > 0,
    },
    [EgenNringModalFormField.registrertINorge]: {
        isIncluded: () => true,
        isAnswered: ({ registrertINorge }) => registrertINorge !== YesOrNo.UNANSWERED,
        visibilityFilter: ({ navnPNringen }) => hasValue(navnPNringen),
    },
    [EgenNringModalFormField.orgnr]: {
        isIncluded: ({ registrertINorge }) => registrertINorge === YesOrNo.YES,
        isAnswered: ({ orgnr }) => hasValue(orgnr),
        visibilityFilter: ({ registrertINorge }) => registrertINorge === YesOrNo.YES,
    },
    [EgenNringModalFormField.land]: {
        isIncluded: ({ registrertINorge }) => registrertINorge === YesOrNo.NO,
        isAnswered: ({ land }) => hasValue(land),
        visibilityFilter: ({ registrertINorge }) => registrertINorge === YesOrNo.NO,
    },
    [EgenNringModalFormField.fom]: {
        isIncluded: () => true,
        isAnswered: ({ fom }) => hasValue(fom),
        visibilityFilter: ({ orgnr, land }) => hasValue(orgnr) || hasValue(land),
    },
    [EgenNringModalFormField.pgende]: {
        isIncluded: () => true,
        isAnswered: ({ pgende }) => pgende !== YesOrNo.UNANSWERED,
        visibilityFilter: ({ fom }) => hasValue(fom),
    },
    [EgenNringModalFormField.tom]: {
        isIncluded: ({ pgende }) => pgende === YesOrNo.NO,
        isAnswered: ({ tom }) => hasValue(tom),
        visibilityFilter: ({ pgende }) => pgende === YesOrNo.NO,
    },
    [EgenNringModalFormField.nringsresultat]: {
        isIncluded: ({ fom }) => erVirksomhetRegnetSomNyoppstartet(getDateFromDateString(fom)),
        isAnswered: ({ nringsresultat }) => hasValue(nringsresultat),
        visibilityFilter: ({ pgende, tom }) => pgende === YesOrNo.YES || hasValue(tom),
    },
    [EgenNringModalFormField.harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene]: {
        isIncluded: ({ fom }) => erVirksomhetRegnetSomNyoppstartet(getDateFromDateString(fom)),
        isAnswered: ({ harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene }) =>
            harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene !== YesOrNo.UNANSWERED,
        visibilityFilter: ({ nringsresultat }) => hasValue(nringsresultat),
    },
    [EgenNringModalFormField.yrkesAktivDato]: {
        isIncluded: ({ fom }) => erVirksomhetRegnetSomNyoppstartet(getDateFromDateString(fom)),
        isAnswered: ({ yrkesAktivDato }) => hasValue(yrkesAktivDato),
        visibilityFilter: ({ harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene }) =>
            harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene === YesOrNo.YES,
    },
    [EgenNringModalFormField.hattVarigEndringAvNringsinntektSiste4Kalenderr]: {
        isIncluded: ({ fom }) => !erVirksomhetRegnetSomNyoppstartet(getDateFromDateString(fom)),
        isAnswered: ({ hattVarigEndringAvNringsinntektSiste4Kalenderr }) =>
            hattVarigEndringAvNringsinntektSiste4Kalenderr !== YesOrNo.UNANSWERED,
        visibilityFilter: ({ pgende, tom }) => pgende === YesOrNo.YES || hasValue(tom),
    },
    [EgenNringModalFormField.datoForEndring]: {
        isIncluded: ({ hattVarigEndringAvNringsinntektSiste4Kalenderr }) =>
            hattVarigEndringAvNringsinntektSiste4Kalenderr === YesOrNo.YES,
        isAnswered: ({ datoForEndring }) => hasValue(datoForEndring),
        visibilityFilter: ({ hattVarigEndringAvNringsinntektSiste4Kalenderr }) =>
            hattVarigEndringAvNringsinntektSiste4Kalenderr === YesOrNo.YES,
    },
    [EgenNringModalFormField.inntektEtterEndring]: {
        isIncluded: ({ hattVarigEndringAvNringsinntektSiste4Kalenderr }) =>
            hattVarigEndringAvNringsinntektSiste4Kalenderr === YesOrNo.YES,
        isAnswered: ({ inntektEtterEndring }) => hasValue(inntektEtterEndring),
        visibilityFilter: ({ datoForEndring }) => hasValue(datoForEndring),
    },
    [EgenNringModalFormField.forklaringEndring]: {
        isIncluded: ({ hattVarigEndringAvNringsinntektSiste4Kalenderr }) =>
            hattVarigEndringAvNringsinntektSiste4Kalenderr === YesOrNo.YES,
        isAnswered: ({ forklaringEndring }) => hasValue(forklaringEndring),
        visibilityFilter: ({ inntektEtterEndring }) => hasValue(inntektEtterEndring),
    },
    [EgenNringModalFormField.harRegnskapsfrer]: {
        isIncluded: () => true,
        isAnswered: ({ harRegnskapsfrer }) => harRegnskapsfrer !== YesOrNo.UNANSWERED,
        visibilityFilter: ({
            yrkesAktivDato,
            forklaringEndring,
            harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene,
            hattVarigEndringAvNringsinntektSiste4Kalenderr,
        }) =>
            hasValue(yrkesAktivDato) ||
            hasValue(forklaringEndring) ||
            harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene === YesOrNo.NO ||
            hattVarigEndringAvNringsinntektSiste4Kalenderr === YesOrNo.NO,
    },
    [EgenNringModalFormField.navnRegnskapsfrer]: {
        isIncluded: ({ harRegnskapsfrer }) => harRegnskapsfrer === YesOrNo.YES,
        isAnswered: ({ navnRegnskapsfrer }) => hasValue(navnRegnskapsfrer),
        visibilityFilter: ({ harRegnskapsfrer }) => harRegnskapsfrer === YesOrNo.YES,
    },
    [EgenNringModalFormField.telefonRegnskapsfrer]: {
        isIncluded: ({ harRegnskapsfrer }) => harRegnskapsfrer === YesOrNo.YES,
        isAnswered: ({ telefonRegnskapsfrer }) => hasValue(telefonRegnskapsfrer),
        visibilityFilter: ({ navnRegnskapsfrer }) => hasValue(navnRegnskapsfrer),
    },
    [EgenNringModalFormField.regnskapsfrerNrVennEllerFamilie]: {
        isIncluded: ({ harRegnskapsfrer }) => harRegnskapsfrer === YesOrNo.YES,
        isAnswered: ({ regnskapsfrerNrVennEllerFamilie }) => regnskapsfrerNrVennEllerFamilie !== YesOrNo.UNANSWERED,
        visibilityFilter: ({ telefonRegnskapsfrer }) => hasValue(telefonRegnskapsfrer),
    },
};

const egenNringModalQuestionsConfig = Questions<EgenNringModalFormData, EgenNringModalFormField>(
    EgenNringModalFormConfig
);

export default egenNringModalQuestionsConfig;
