import React from 'react';
import { Story } from '@storybook/react';
import MockAdapter from 'axios-mock-adapter/types';

import { SkerinfoDTO, SkerinfoDTOArbeidsforhold } from 'app/types/SkerinfoDTO';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import SknadSendt from 'app/pages/sknadSendt/SknadSendt';
import withIntlProvider from '../../decorators/withIntl';
import ForeldrepengerStateMock from '../../utils/ForeldrepengerStateMock';
import withForeldrepengersknadContext from '../../decorators/withForeldrepengersknadContext';
import AxiosMock from '../../utils/AxiosMock';

export default {
    title: 'pages/SknadSendt',
    component: SknadSendt,
    decorators: [withIntlProvider, withForeldrepengersknadContext],
};

interface Props {
    erFerdig: boolean;
    bankkonto?: {
        kontonummer: number;
    };
    arbeidsforhold: SkerinfoDTOArbeidsforhold[];
}

const Template: Story<Props> = ({ erFerdig, bankkonto, arbeidsforhold }) => {
    const restMock = (apiMock: MockAdapter) => {
        if (erFerdig) {
            apiMock.onPost('/storage/kvittering/foreldrepenger').replyOnce(200, {});
        }
    };
    return (
        <AxiosMock mock={restMock}>
            <ForeldrepengerStateMock
                sknad={{} as ForeldrepengesknadContextState}
                skerinfo={
                    {
                        sker: { fnr: '1233434', fornavn: 'Espen', etternavn: 'Utvikler', bankkonto, arbeidsforhold },
                    } as SkerinfoDTO
                }
            >
                <SknadSendt />
            </ForeldrepengerStateMock>
        </AxiosMock>
    );
};

export const Default = Template.bind({});;
Default.args = {
    erFerdig: true,
};

export const MedBankkonto = Template.bind({});;
MedBankkonto.args = {
    erFerdig: true,
    bankkonto: { kontonummer: 5646464535 },
};

export const MedArbeidsforhold = Template.bind({});;
MedArbeidsforhold.args = {
    erFerdig: true,
    arbeidsforhold: [{} as SkerinfoDTOArbeidsforhold],
};

export const VenterPLagring = Template.bind({});
VenterPLagring.args = {
    erFerdig: false,
};



