import { intlUtils, Step } from '@navikt/fp-common';
import useAvbrytSknad from 'app/utils/hooks/useAvbrytSknad';
import React from 'react';
import { useIntl } from 'react-intl';
import stepConfig, { getPreviousStepHref } from '../stepsConfig';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import useSknad from 'app/utils/hooks/useSknad';
import Api from 'app/api/api';
import UttaksplanInfoScenarios from './components/UttaksplanInfoScenarios';
import getStnadskontoParams from 'app/api/getStnadskontoParams';
import { Dekningsgrad } from 'app/types/Dekningsgrad';
import NavFrontendSpinner from 'nav-frontend-spinner';
import { getRegistrertBarnOmDetFinnes } from 'app/utils/barnUtils';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';

const UttaksplanInfo = () => {
    const intl = useIntl();

    const skerinfo = useSkerinfo();
    const sknad = useSknad();

    const { barn, annenForelder, skersituasjon } = sknad;
    const { registrerteBarn } = skerinfo;
    const erFarEllerMedmor = isFarEllerMedmor(skersituasjon.rolle);

    const registrertBarn = getRegistrertBarnOmDetFinnes(barn, registrerteBarn);

    const { eksisterendeSakAnnenPartData } = Api.useGetEksisterendeSakMedFnr(
        skerinfo.person.fnr,
        erFarEllerMedmor,
        registrertBarn?.annenForelder?.fnr
    );
    const { tilgjengeligeStnadskontoerData: stnadskontoer100 } = Api.useGetUttakskontoer(
        getStnadskontoParams(Dekningsgrad.HUNDRE_PROSENT, barn, annenForelder, skersituasjon)
    );
    const { tilgjengeligeStnadskontoerData: stnadskontoer80 } = Api.useGetUttakskontoer(
        getStnadskontoParams(Dekningsgrad.TTI_PROSENT, barn, annenForelder, skersituasjon)
    );
    const onAvbrytSknad = useAvbrytSknad();

    if (
        !stnadskontoer100 ||
        !stnadskontoer80 ||
        (!!registrertBarn && !eksisterendeSakAnnenPartData && erFarEllerMedmor)
    ) {
        return (
            <div style={{ textAlign: 'center', padding: '12rem 0' }}>
                <NavFrontendSpinner type="XXL" />
            </div>
        );
    }

    return (
        <Step
            bannerTitle={intlUtils(intl, 'sknad.pageheading')}
            backLinkHref={getPreviousStepHref('uttaksplanInfo')}
            activeStepId="uttaksplanInfo"
            pageTitle={intlUtils(intl, 'sknad.skersituasjon')}
            stepTitle={intlUtils(intl, 'sknad.skersituasjon')}
            onCancel={onAvbrytSknad}
            steps={stepConfig}
            kompakt={true}
        >
            <UttaksplanInfoScenarios
                tilgjengeligeStnadskontoer100DTO={stnadskontoer100}
                tilgjengeligeStnadskontoer80DTO={stnadskontoer80}
                eksisterendeSakAnnenPart={eksisterendeSakAnnenPartData}
            />
        </Step>
    );
};

export default UttaksplanInfo;
