import { Block, intlUtils, Kjnn, Step } from '@navikt/fp-common';
import actionCreator from 'app/context/action/actionCreator';
import SknadRoutes from 'app/routes/routes';
import { Hovedknapp } from 'nav-frontend-knapper';
import React from 'react';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import useSknad from 'app/utils/hooks/useSknad';
import useAvbrytSknad from 'app/utils/hooks/useAvbrytSknad';
import { useIntl } from 'react-intl';
import stepConfig from '../stepsConfig';
import VelgRolle from './components/VelgRolle';
import {
    getInitialSkerSituasjonValues,
    SkersituasjonFormComponents,
    SkersituasjonFormData,
    SkersituasjonFormField,
} from './skersituasjonFormConfig';
import skersituasjonQuestionsConfig from './skersituasjonQuestionsConfig';
import { mapSkersituasjonFormDataToState } from './skersituasjonUtils';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';

interface Props {
    kjnn: Kjnn;
}

const Skersituasjon: React.FunctionComponent<Props> = ({ kjnn }) => {
    const intl = useIntl();
    const sknad = useSknad();
    const skerinfo = useSkerinfo();

    const onValidSubmitHandler = (values: Partial<SkersituasjonFormData>) => {
        const skersituasjon = mapSkersituasjonFormDataToState(values);
        return [actionCreator.setSkersituasjon(skersituasjon)];
    };

    const onValidSubmit = useOnValidSubmit(onValidSubmitHandler, SknadRoutes.OM_BARNET);
    const onAvbrytSknad = useAvbrytSknad();

    return (
        <SkersituasjonFormComponents.FormikWrapper
            initialValues={getInitialSkerSituasjonValues(sknad.skersituasjon)}
            onSubmit={onValidSubmit}
            renderForm={({ values: formValues }) => {
                const visibility = skersituasjonQuestionsConfig.getVisbility({
                    ...formValues,
                    skerKjnn: skerinfo.person.kjnn,
                });
                const allQuestionsAnswered = visibility.areAllQuestionsAnswered();
                return (
                    <Step
                        bannerTitle={intlUtils(intl, 'sknad.pageheading')}
                        activeStepId="skersituasjon"
                        pageTitle={intlUtils(intl, 'sknad.skersituasjon')}
                        stepTitle={intlUtils(intl, 'sknad.skersituasjon')}
                        onCancel={onAvbrytSknad}
                        onContinueLater={() => null}
                        steps={stepConfig}
                        kompakt={true}
                    >
                        <SkersituasjonFormComponents.Form includeButtons={false}>
                            <div>
                                <Block margin="xl">
                                    <SkersituasjonFormComponents.RadioPanelGroup
                                        name={SkersituasjonFormField.situasjon}
                                        radios={[
                                            {
                                                label: intlUtils(intl, 'skersituasjon.radioButton.fdsel'),
                                                value: 'fdsel',
                                            },
                                            {
                                                label: intlUtils(intl, 'skersituasjon.radioButton.adopsjon'),
                                                value: 'adopsjon',
                                            },
                                        ]}
                                        useTwoColumns={true}
                                        legend={intlUtils(intl, 'skersituasjon.text.situasjon')}
                                    />
                                </Block>
                                <Block visible={visibility.isVisible(SkersituasjonFormField.rolle)} margin="xl">
                                    <VelgRolle kjnn={kjnn} />
                                </Block>
                                {allQuestionsAnswered && (
                                    <Block margin="xl" textAlignCenter={true}>
                                        <Hovedknapp>{intlUtils(intl, 'sknad.gVidere')}</Hovedknapp>
                                    </Block>
                                )}
                            </div>
                        </SkersituasjonFormComponents.Form>
                    </Step>
                );
            }}
        />
    );
};

export default Skersituasjon;
