import { FamiliehendelseType } from 'app/types/FamiliehendelseType';

export const getFamiliehendelseType = (fdselsdato: string | undefined, termindato: string | undefined) => {
    if (fdselsdato !== undefined) {
        return FamiliehendelseType.FDSEL;
    } else {
        return termindato !== undefined ? FamiliehendelseType.TERM : FamiliehendelseType.ADOPSJON;
    }
};
