import {
    barnetErUnder15rPAdopsjonsdato,
    erIUke22Pluss3,
    erMindreEnn3UkerSiden,
    etterDagensDato,
    hasValue,
    intlUtils,
} from '@navikt/fp-common';
import dayjs from 'dayjs';
import { isISODateString } from 'nav-datovelger';
import { IntlShape } from 'react-intl';
import isSameOrAfter from 'dayjs/plugin/isSameOrAfter';
import isSameOrBefore from 'dayjs/plugin/isSameOrBefore';
import { isDateABeforeDateB } from 'app/utils/dateUtils';

dayjs.extend(isSameOrAfter);
dayjs.extend(isSameOrBefore);

export const validateFdselsdato = (intl: IntlShape) => (fdselsdato: string) => {
    if (!hasValue(fdselsdato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.fdselsdato.duMOppgi');
    }

    if (!isISODateString(fdselsdato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.fdselsdato.ugyldigDatoFormat');
    }

    if (etterDagensDato(fdselsdato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.fdselsdato.mVreIdagEllerTidligere');
    }

    return undefined;
};

export const validateFdselsdatoAdopsjon = (intl: IntlShape) => (fdselsdato: string, adopsjonsdato: string) => {
    if (!hasValue(fdselsdato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.fdselsdato.duMOppgi');
    }

    if (!isISODateString(fdselsdato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.fdselsdato.ugyldigDatoFormat');
    }

    if (etterDagensDato(fdselsdato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.fdselsdato.mVreIdagEllerTidligere');
    }

    if (isDateABeforeDateB(adopsjonsdato, fdselsdato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.fdselsdato.mVreFrAdopsjonsdato');
    }

    if (!barnetErUnder15rPAdopsjonsdato(fdselsdato, adopsjonsdato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.fdselsdato.ikkeMerEnn15r3MndTilbake');
    }

    return undefined;
};

export const validateTermindato = (intl: IntlShape) => (termindato: string) => {
    if (!hasValue(termindato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.termindato.duMOppgi');
    }

    if (!isISODateString(termindato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.termindato.ugyldigDatoFormat');
    }

    if (!erMindreEnn3UkerSiden(termindato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.termindato.forTidlig');
    }

    if (!erIUke22Pluss3(termindato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.termindato.duMVreIUke22');
    }
};

export const validateTermindatoFdsel = (intl: IntlShape) => (termindato: string) => {
    if (!hasValue(termindato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.termindato.duMOppgi');
    }

    if (!isISODateString(termindato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.termindato.ugyldigDatoFormat');
    }

    if (!dayjs().add(9, 'months').isSameOrAfter(dayjs(termindato), 'day')) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.termindato.forLangtFremITid');
    }
};

export const validateAdopsjonsdato = (intl: IntlShape) => (adopsjonsdato: string) => {
    if (!hasValue(adopsjonsdato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.adopsjonsdato.duMOppgi');
    }

    if (!isISODateString(adopsjonsdato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.adopsjonsdato.ugyldigDatoFormat');
    }
};

export const validateAnkomstdato = (intl: IntlShape) => (ankomstdato: string, fdselsdato: string) => {
    if (!hasValue(ankomstdato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.ankomstDato.duMOppgi');
    }

    if (!isISODateString(ankomstdato)) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.ankomstDato.ugyldigDatoFormat');
    }

    if (!dayjs(fdselsdato).isSameOrBefore(ankomstdato, 'day')) {
        return intlUtils(intl, 'valideringsfeil.omBarnet.ankomstDato.frFdselsdato');
    }
};
