import { Block, intlUtils } from '@navikt/fp-common';
import { YesOrNo } from '@navikt/sif-common-formik/lib';
import { QuestionVisibility } from '@navikt/sif-common-question-config/lib';
import { Nring } from 'app/context/types/Nring';
import { Knapp } from 'nav-frontend-knapper';
import React, { FunctionComponent, useState } from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import {
    InntektsinformasjonFormComponents,
    InntektsinformasjonFormData,
    InntektsinformasjonFormField,
} from '../../inntektsinformasjonFormConfig';
import EgenNringListe from './EgenNringListe';
import HvemKanDriveMedEgenNring from './HvemKanDriveMedEgenNring';
import EgenNringModal from './modal/EgenNringModal';

interface Props {
    egenNringInformasjon: Nring[];
    setEgenNringsInformasjon: (oppdrag: Nring[]) => void;
    visibility: QuestionVisibility<InntektsinformasjonFormField, undefined>;
    formValues: InntektsinformasjonFormData;
}

const EgenNring: FunctionComponent<Props> = ({
    egenNringInformasjon,
    setEgenNringsInformasjon,
    visibility,
    formValues,
}) => {
    const intl = useIntl();
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [selectedNring, setSelectedNring] = useState<Nring>();

    const handleOnLeggTil = () => {
        setIsModalOpen(true);
        setSelectedNring(undefined);
    };

    const addNring = (nring: Nring) => {
        const updatedEgenNringInformasjon = egenNringInformasjon.concat(nring);

        setEgenNringsInformasjon(updatedEgenNringInformasjon);
    };

    const deleteNring = (nring: Nring) => {
        const updatedEgenNringInformasjon = egenNringInformasjon.filter((nr) => nr !== nring);

        setEgenNringsInformasjon(updatedEgenNringInformasjon);
    };

    const editNring = (nring: Nring) => {
        const updatedEgenNringInformasjon = egenNringInformasjon
            .filter((nr) => nr !== selectedNring)
            .concat(nring);

        setEgenNringsInformasjon(updatedEgenNringInformasjon);
    };

    const selectNring = (nring: Nring) => {
        setSelectedNring(nring);
        setIsModalOpen(true);
    };

    return (
        <>
            <Block
                padBottom="l"
                visible={visibility.isVisible(InntektsinformasjonFormField.hattInntektSomNringsdrivende)}
            >
                <InntektsinformasjonFormComponents.YesOrNoQuestion
                    name={InntektsinformasjonFormField.hattInntektSomNringsdrivende}
                    legend={intlUtils(intl, 'inntektsinformasjon.harJobbetSomSelvstendigNringsdrivendeSiste10Mnd')}
                    description={<HvemKanDriveMedEgenNring />}
                />
            </Block>
            {formValues.hattInntektSomNringsdrivende === YesOrNo.YES && (
                <div style={{ backgroundColor: '#e9e7e7', marginBottom: '1rem', padding: '1rem' }}>
                    <EgenNringModal
                        isOpen={isModalOpen}
                        title="Nringsinformasjon"
                        onRequestClose={() => setIsModalOpen(false)}
                        selectedNring={selectedNring}
                        addNring={addNring}
                        editNring={editNring}
                    />
                    <Block padBottom="l" visible={egenNringInformasjon.length > 0}>
                        <EgenNringListe
                            nringsInformasjon={egenNringInformasjon}
                            deleteNring={deleteNring}
                            selectNring={selectNring}
                        />
                    </Block>
                    <Knapp htmlType="button" onClick={handleOnLeggTil}>
                        <FormattedMessage id="inntektsinformasjon.leggTilVirksomhet" />
                    </Knapp>
                </div>
            )}
        </>
    );
};

export default EgenNring;
