import React, { createContext, Dispatch, FunctionComponent, ReactNode, useMemo, useReducer } from 'react';
import { ForeldrepengesknadContextAction } from './action/actionCreator';
import { ForeldrepengesknadContextState, foreldrepengesknadInitialState } from './ForeldrepengesknadContextConfig';
import foreldrepengesknadReducer from './reducer/foreldrepengesknadReducer';

interface ForeldrepengesknadContextData {
    state: ForeldrepengesknadContextState;
    dispatch: Dispatch<ForeldrepengesknadContextAction>;
}

export const ForeldrepengesknadContext = createContext<ForeldrepengesknadContextData>(null!);

interface Props {
    children: ReactNode;
}

const ForeldrepengesknadContextProvider: FunctionComponent<Props> = ({ children }) => {
    const [state, dispatch] = useReducer(foreldrepengesknadReducer, foreldrepengesknadInitialState);

    const contextValue = useMemo(() => {
        return { state, dispatch };
    }, [state, dispatch]);

    return <ForeldrepengesknadContext.Provider value={contextValue}>{children}</ForeldrepengesknadContext.Provider>;
};

export default ForeldrepengesknadContextProvider;
