import * as React from 'react';
import { FormattedMessage, useIntl } from 'react-intl';
import dayjs from 'dayjs';
import Veilederpanel from 'nav-frontend-veilederpanel';
import { Block, intlUtils } from '@navikt/fp-common';
import { formaterDato, getVarighetString } from 'app/utils/dateUtils';
import Veileder from 'app/components/veileder/Veileder';
import { Uttaksdagen } from '../../../utils/Uttaksdagen';

type ugyldigStartdatorsak = undefined | 'helgedag' | 'fortidlig';

export interface Props {
    startdato: Date | undefined;
    antallDager: number;
    antallDagerFrFdselIhtRegler: number;
    skalIkkeHaUttakFrTermin: boolean | undefined;
    frsteMuligeStartdato: Date | undefined;
    visKunFeil?: boolean;
}

export const getrsakDersomUgyldigStartdato = (
    startdato: Date | undefined,
    frsteMuligeStartdato: Date | undefined
): ugyldigStartdatorsak => {
    if (startdato === undefined) {
        return;
    }
    if (Uttaksdagen(startdato).erUttaksdag() === false) {
        return 'helgedag';
    } else if (frsteMuligeStartdato && dayjs(startdato).isBefore(frsteMuligeStartdato)) {
        return 'fortidlig';
    }
    return;
};

const VeilederStartdatoPermisjon: React.FunctionComponent<Props> = ({
    startdato,
    antallDager,
    antallDagerFrFdselIhtRegler,
    skalIkkeHaUttakFrTermin,
    frsteMuligeStartdato,
    visKunFeil,
}) => {
    const intl = useIntl();

    const ugyldigDatorsak = startdato && getrsakDersomUgyldigStartdato(startdato, frsteMuligeStartdato);

    const msg = React.useMemo(() => {
        if (ugyldigDatorsak && frsteMuligeStartdato) {
            return ugyldigDatorsak === 'fortidlig'
                ? intlUtils(intl, 'uttaksplaninfo.veileder.forTidligStartdato', {
                      dato: formaterDato(frsteMuligeStartdato),
                  })
                : intlUtils(intl, 'uttaksplaninfo.veileder.helgedag');
        }
        if (visKunFeil) {
            return undefined;
        }
        if (skalIkkeHaUttakFrTermin === true) {
            return <FormattedMessage id="uttaksplaninfo.informasjon.foreldrepengerFrFdselSkalIkkeHa" />;
        } else if (antallDager < antallDagerFrFdselIhtRegler) {
            return (
                <FormattedMessage
                    id="uttaksplaninfo.informasjon.foreldrepengerFrFdselMindreEnnTreUker"
                    values={{
                        varighet: getVarighetString(antallDagerFrFdselIhtRegler - antallDager, intl),
                    }}
                />
            );
        } else if (antallDager > antallDagerFrFdselIhtRegler) {
            return (
                <FormattedMessage
                    id="uttaksplaninfo.informasjon.foreldrepengerFrFdselMerEnnTreUker"
                    values={{
                        varighet: getVarighetString(antallDager - antallDagerFrFdselIhtRegler, intl),
                    }}
                />
            );
        }
        return undefined;
    }, [
        ugyldigDatorsak,
        antallDager,
        antallDagerFrFdselIhtRegler,
        frsteMuligeStartdato,
        intl,
        skalIkkeHaUttakFrTermin,
        visKunFeil,
    ]);

    const erFeilmelding = ugyldigDatorsak !== undefined;

    return (
        <Block margin="none" visible={msg !== undefined}>
            <Veilederpanel svg={<Veileder farge="lilla" ansikt={erFeilmelding ? 'skeptisk' : 'glad'} stil="kompakt" />}>
                {msg}
            </Veilederpanel>
        </Block>
    );
};

export default VeilederStartdatoPermisjon;
