import { hasValue } from '@navikt/fp-common';
import { YesOrNo } from '@navikt/sif-common-formik/lib';
import { QuestionConfig, Questions } from '@navikt/sif-common-question-config/lib';
import {
    FarMedmorAleneomsorgFdselFormData,
    FarMedmorAleneomsorgFdselFormField,
} from './farMedmorAleneomsorgFdselFormConfig';

const FarMedmorAleneomsorgFdselFormConfig: QuestionConfig<
    FarMedmorAleneomsorgFdselFormData,
    FarMedmorAleneomsorgFdselFormField
> = {
    [FarMedmorAleneomsorgFdselFormField.dekningsgrad]: {
        isIncluded: () => true,
        isAnswered: ({ dekningsgrad }) => hasValue(dekningsgrad),
    },
    [FarMedmorAleneomsorgFdselFormField.startPOmsorgsovertakelse]: {
        isIncluded: () => true,
        isAnswered: ({ startPOmsorgsovertakelse }) => startPOmsorgsovertakelse !== YesOrNo.UNANSWERED,
        visibilityFilter: ({ dekningsgrad }) => hasValue(dekningsgrad),
    },
    [FarMedmorAleneomsorgFdselFormField.startdatoUttak]: {
        isIncluded: ({ startPOmsorgsovertakelse }) => startPOmsorgsovertakelse === YesOrNo.NO,
        isAnswered: ({ startdatoUttak }) => hasValue(startdatoUttak),
        visibilityFilter: ({ startPOmsorgsovertakelse }) => startPOmsorgsovertakelse === YesOrNo.NO,
    },
};

const farMedmorAleneomsorgFdselQuestionsConfig = Questions<
    FarMedmorAleneomsorgFdselFormData,
    FarMedmorAleneomsorgFdselFormField
>(FarMedmorAleneomsorgFdselFormConfig);

export default farMedmorAleneomsorgFdselQuestionsConfig;
