import { FarMedmorFrstegangssknadMedAnnenPartUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import { FarMedmorFrstegangssknadMedAnnenPartFormData } from './farMedmorFrstegangssknadMedAnnenPartFormConfig';

export const getFarMedmorFrstegangssknadMedAnnenPartInitialValues = (
    state: FarMedmorFrstegangssknadMedAnnenPartUttaksplanInfo | undefined
): FarMedmorFrstegangssknadMedAnnenPartFormData => {
    if (!state) {
        return {
            permisjonStartdato: '',
        };
    }

    return {
        permisjonStartdato: state.permisjonStartdato,
    };
};
