import React from 'react';
import { Story } from '@storybook/react';
import MockAdapter from 'axios-mock-adapter/types';

import skerinfo from './testdata/skerinfo.json';
import context from './testdata/context.json';
import { SkerinfoDTO } from 'app/types/SkerinfoDTO';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import AnnenForelder from 'app/steps/annen-forelder/AnnenForelder';
import withIntlProvider from '../../../decorators/withIntl';
import withRouter from '../../../decorators/withRouter';
import withForeldrepengersknadContext from '../../../decorators/withForeldrepengersknadContext';
import ForeldrepengerStateMock from '../../../utils/ForeldrepengerStateMock';
import AxiosMock from '../../../utils/AxiosMock';

export default {
    title: 'steps/AnnenForelder',
    component: AnnenForelder,
    decorators: [withRouter, withIntlProvider, withForeldrepengersknadContext],
};

interface Props {
    context: ForeldrepengesknadContextState;
    skerinfo: SkerinfoDTO;
}

const Template: Story<Props> = ({ context, skerinfo }) => {
    const restMock = (apiMock: MockAdapter) => {
        apiMock.onPost('/storage/vedlegg').reply(
            200,
            { data: {} },
            {
                location: '',
            }
        );
    };
    return (
        <AxiosMock mock={restMock}>
            <ForeldrepengerStateMock sknad={context} skerinfo={skerinfo}>
                <AnnenForelder />
            </ForeldrepengerStateMock>
        </AxiosMock>
    );
};

export const Default = Template.bind({});;
Default.args = {
    context,
    skerinfo,
};

export const SkalOppgiPersonalia = Template.bind({});;
SkalOppgiPersonalia.args = {
    context: {
        ...context,
        sknad: {
            ...context.sknad,
            annenForelder: {
                kanIkkeOppgis: false,
            },
        },
    } as ForeldrepengesknadContextState,
    skerinfo: {
        sker: {
            ...skerinfo,
            barn: [],
        },
    } as SkerinfoDTO,
};

export const ForFar = Template.bind({});
ForFar.args = {
    context: {
        ...context,
        sknad: {
            ...context.sknad,
            skersituasjon: {
                situasjon: 'fdsel',
                rolle: 'far',
            },
        },
    } as ForeldrepengesknadContextState,
    skerinfo: {
        sker: {
            ...skerinfo.sker,
            fornavn: 'LEALAUS',
            etternavn: 'BREPOSE',
            kjnn: 'M',
            barn: [
                {
                    fnr: '21091981146',
                    fdselsdato: '2021-03-15',
                    annenForelder: {
                        fnr: '12038517080',
                        fdselsdato: '1985-03-12',
                        fornavn: 'TALENTFULL',
                        etternavn: 'MYGG',
                    },
                    fornavn: 'KLKTIG',
                    etternavn: 'MIDTPUNKT',
                    kjnn: 'K',
                },
            ],
        },
    } as SkerinfoDTO,
};



