import { dateToISOString } from '@navikt/sif-common-formik/lib';
import { MorFdselUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import { Dekningsgrad } from 'app/types/Dekningsgrad';
import { MorFdselFormData, MorFdselFormField } from './morFdselFormConfig';

const initialMorFdselValues: MorFdselFormData = {
    [MorFdselFormField.dekningsgrad]: '',
    [MorFdselFormField.permisjonStartdato]: '',
    [MorFdselFormField.skalIkkeHaUttakFrTermin]: false,
    [MorFdselFormField.fellesperiodeukerMor]: undefined,
};

export const mapMorFdselFormToState = (values: Partial<MorFdselFormData>): MorFdselUttaksplanInfo => {
    return {
        permisjonStartdato: values.permisjonStartdato!,
        skalIkkeHaUttakFrTermin: values.skalIkkeHaUttakFrTermin!,
        fellesperiodeukerMor: values.fellesperiodeukerMor,
    };
};

export const getInitialMorFdselValues = (
    defaultPermisjonStartdato: Date,
    lagretUttaksplanInfo: MorFdselUttaksplanInfo | undefined,
    dekningsgrad: Dekningsgrad
): MorFdselFormData => {
    if (lagretUttaksplanInfo) {
        return {
            ...lagretUttaksplanInfo,
            [MorFdselFormField.dekningsgrad]: dekningsgrad,
        };
    }

    return {
        ...initialMorFdselValues,
        [MorFdselFormField.permisjonStartdato]: dateToISOString(defaultPermisjonStartdato),
    };
};
