import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import { Dekningsgrad } from 'app/types/Dekningsgrad';
import { Kvittering } from 'app/types/Kvittering';
import Sak from 'app/types/Sak';
import { StorageKvittering } from 'app/types/StorageKvittering';
import { SkerinfoDTO } from 'app/types/SkerinfoDTO';
import { useRequest } from 'app/utils/hooks/useRequest';
import { AxiosResponse } from 'axios';
import getAxiosInstance from './apiInterceptor';
import { storageParser } from './storageParser';
import Environment from 'app/Environment';
import { TilgjengeligeStnadskontoerDTO } from 'app/types/TilgjengeligeStnadskontoerDTO';
import { EksisterendeSakDTO } from 'app/types/EksisterendeSakDTO';
import { mapEksisterendeSakFromDTO } from 'app/utils/eksisterendeSakUtils';
import { formaterDato } from 'app/utils/dateUtils';

export interface TilgjengeligeStnadskontoerParams {
    antallBarn: string;
    morHarRett: boolean;
    farHarRett: boolean;
    dekningsgrad: Dekningsgrad.HUNDRE_PROSENT | Dekningsgrad.TTI_PROSENT;
    termindato: string | undefined;
    fdselsdato: string | undefined;
    omsorgsovertakelsesdato: string | undefined;
    morHarAleneomsorg: boolean | undefined;
    farHarAleneomsorg: boolean | undefined;
    startdatoUttak: string;
}

const formaterStnadskontoParamsDatoer = (dato: string | undefined, datoformat?: string): string | undefined => {
    return dato !== undefined ? formaterDato(dato, datoformat) : undefined;
};

const uttakBaseUrl = Environment.UTTAK_API_URL;
// const sendSknadUrl = '/soknad';
// const sendEndringssknadUrl = '/soknad/endre';

const useSkerinfo = () => {
    const { data, error } = useRequest<SkerinfoDTO>('/sokerinfo', { config: { withCredentials: true } });

    return {
        skerinfoData: data,
        skerinfoError: error,
    };
};

const useGetSaker = () => {
    const { data, error } = useRequest<Sak[]>('/innsyn/saker', { fnr: '123', config: { withCredentials: true } });

    return {
        sakerData: data,
        sakerError: error,
    };
};

const useGetEksisterendeSak = (saksnummer: string) => {
    const { data, error } = useRequest<EksisterendeSakDTO>('/innsyn/uttaksplan', {
        fnr: '123',
        config: { withCredentials: true, params: saksnummer },
    });

    return {
        eksisterendeSakData: data,
        eksisterendeSakError: error,
    };
};

const useGetEksisterendeSakMedFnr = (skerFnr: string, erFarEllerMedmor: boolean, annenPartFnr: string | undefined) => {
    const { data, error } = useRequest<EksisterendeSakDTO>('/innsyn/uttaksplanannen', {
        fnr: skerFnr,
        config: { params: { annenPart: annenPartFnr }, withCredentials: true },
        isSuspended: annenPartFnr && erFarEllerMedmor ? false : true,
    });

    return {
        eksisterendeSakAnnenPartData: mapEksisterendeSakFromDTO(data, erFarEllerMedmor, true),
        eksisterendeSakAnnenPartError: error,
    };
};

const useStoredAppState = () => {
    const { data, error } = useRequest<ForeldrepengesknadContextState>('/storage', {
        config: { transformResponse: storageParser, withCredentials: true },
    });

    return {
        storageData: data,
        storageError: error,
    };
};

const storeAppState = (state: ForeldrepengesknadContextState) => {
    const { sknad, version, currentRoute, uttaksplanInfo } = state;
    return getAxiosInstance('123').post(
        '/storage',
        { sknad, version, currentRoute, uttaksplanInfo },
        { withCredentials: true }
    );
};

const deleteStoredAppState = () => {
    return getAxiosInstance('123').delete('/storage', { withCredentials: true });
};

const sendStorageKvittering = (
    storageKvittering: StorageKvittering,
    fnr: string
): Promise<AxiosResponse<Kvittering>> => {
    return getAxiosInstance(fnr).post('/storage/kvittering/foreldrepenger', storageKvittering, {
        withCredentials: true,
        timeout: 15 * 1000,
    });
};

const useGetUttakskontoer = (params: TilgjengeligeStnadskontoerParams) => {
    const {
        antallBarn,
        farHarRett,
        morHarRett,
        dekningsgrad,
        fdselsdato,
        termindato,
        omsorgsovertakelsesdato,
        morHarAleneomsorg,
        farHarAleneomsorg,
        startdatoUttak,
    } = params;

    const fpUttakServiceDateFormat = 'YYYYMMDD';

    const urlParams = {
        farHarRett,
        morHarRett,
        morHarAleneomsorg: morHarAleneomsorg || false,
        farHarAleneomsorg: farHarAleneomsorg || false,
        dekningsgrad,
        antallBarn,
        fdselsdato: formaterStnadskontoParamsDatoer(fdselsdato, fpUttakServiceDateFormat),
        termindato: formaterStnadskontoParamsDatoer(termindato, fpUttakServiceDateFormat),
        omsorgsovertakelseDato: formaterStnadskontoParamsDatoer(omsorgsovertakelsesdato, fpUttakServiceDateFormat),
        startdatoUttak: formaterStnadskontoParamsDatoer(startdatoUttak, fpUttakServiceDateFormat),
    };

    const { data, error } = useRequest<TilgjengeligeStnadskontoerDTO>(`${uttakBaseUrl}/konto`, {
        config: {
            timeout: 15 * 1000,
            params: urlParams,
            withCredentials: false,
        },
        isSuspended: false,
    });

    return {
        tilgjengeligeStnadskontoerData: data,
        tilgjengeligeStnadskontoerError: error,
    };
};

