const Api = {
    useGetSaker,
    useGetUttakskontoer,
    storeAppState,
    deleteStoredAppState,
    sendStorageKvittering,
    useGetEksisterendeSakMedFnr,
    useStoredAppState,
    useSkerinfo,
    useGetEksisterendeSak,
    // sendSknad,
    // getStorageKvittering,
};

export default Api;
