import { intlUtils } from '@navikt/fp-common';
import { IntlShape } from 'react-intl';

export const validateHarForstttRettigheterOgPlikter = (intl: IntlShape) => (value: boolean) => {
    if (value !== true) {
        return intlUtils(intl, 'valideringsfeil.velkommen.harForstttRettigheterOgPlikter.pkrevd');
    }

    return undefined;
};
