import React, { FunctionComponent } from 'react';
import { useIntl } from 'react-intl';
import { TypedFormComponents } from '@navikt/sif-common-formik/lib';
import { intlUtils, UtvidetInformasjon } from '@navikt/fp-common';
import { Dekningsgrad } from 'app/types/Dekningsgrad';
import { getAntallUker } from 'app/steps/uttaksplan-info/utils/stnadskontoer';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';

interface Props {
    FormKomponent: TypedFormComponents<any, any, any>;
    dekningsgradFeltNavn: string;
    tilgjengeligeStnadskontoer: {
        [Dekningsgrad.TTI_PROSENT]: TilgjengeligStnadskonto[];
        [Dekningsgrad.HUNDRE_PROSENT]: TilgjengeligStnadskonto[];
    };
}

const DekningsgradSprsml: FunctionComponent<Props> = ({
    FormKomponent,
    dekningsgradFeltNavn,
    tilgjengeligeStnadskontoer,
}) => {
    const intl = useIntl();

    return (
        <FormKomponent.RadioPanelGroup
            name={dekningsgradFeltNavn}
            radios={[
                {
                    label: intlUtils(intl, 'uttaksplaninfo.49Uker', {
                        antallUker: getAntallUker(tilgjengeligeStnadskontoer[Dekningsgrad.HUNDRE_PROSENT]),
                    }),
                    value: Dekningsgrad.HUNDRE_PROSENT,
                },
                {
                    label: intlUtils(intl, 'uttaksplaninfo.59Uker', {
                        antallUker: getAntallUker(tilgjengeligeStnadskontoer[Dekningsgrad.TTI_PROSENT]),
                    }),
                    value: Dekningsgrad.TTI_PROSENT,
                },
            ]}
            legend={intlUtils(intl, 'uttaksplaninfo.dekningsgrad.label.deltUttak')}
            description={
                <UtvidetInformasjon apneLabel="Les mer om lengden p foreldrepengeperioden">
                    Den totale utbetalingen blir hyere hvis du velger 100 prosent. Valget gjelder dere begge, og kan
                    ikke endres senere.
                </UtvidetInformasjon>
            }
            useTwoColumns={true}
        />
    );
};

export default DekningsgradSprsml;
