import React, { FunctionComponent } from 'react';
import { QuestionVisibility } from '@navikt/sif-common-question-config/lib';
import { EgenNringModalFormComponents, EgenNringModalFormField } from '../egenNringModalFormConfig';
import { Block, intlUtils } from '@navikt/fp-common';
import Veilederpanel from 'nav-frontend-veilederpanel';
import VeilederNormal from 'app/assets/VeilederNormal';
import { FormattedMessage, useIntl } from 'react-intl';
import { Normaltekst } from 'nav-frontend-typografi';
import { validateNumber } from '../validation/egenNringValidation';

interface Props {
    visibility: QuestionVisibility<EgenNringModalFormField, undefined>;
}

const Regnskapsfrer: FunctionComponent<Props> = ({ visibility }) => {
    const intl = useIntl();

    return (
        <>
            <Block padBottom="l" visible={visibility.isVisible(EgenNringModalFormField.harRegnskapsfrer)}>
                <EgenNringModalFormComponents.YesOrNoQuestion
                    name={EgenNringModalFormField.harRegnskapsfrer}
                    legend={intlUtils(intl, 'inntektsinformasjon.egenNringModal.harRegnskapsfrer')}
                />
            </Block>
            <Block padBottom="l" visible={visibility.isVisible(EgenNringModalFormField.navnRegnskapsfrer)}>
                <EgenNringModalFormComponents.Input
                    name={EgenNringModalFormField.navnRegnskapsfrer}
                    label={intlUtils(intl, 'inntektsinformasjon.egenNringModal.regnskapsfrerNavn')}
                />
            </Block>
            <Block padBottom="l" visible={visibility.isVisible(EgenNringModalFormField.telefonRegnskapsfrer)}>
                <EgenNringModalFormComponents.NumberInput
                    name={EgenNringModalFormField.telefonRegnskapsfrer}
                    label={intlUtils(intl, 'inntektsinformasjon.egenNringModal.regnskapsfrerTlf')}
                    validate={validateNumber(
                        intl,
                        'valideringsfeil.inntektsinformasjon.regnskapsfrerTelefonnummer.ugyldigFormat'
                    )}
                />
            </Block>
            <Block
                padBottom="l"
                visible={visibility.isVisible(EgenNringModalFormField.regnskapsfrerNrVennEllerFamilie)}
            >
                <EgenNringModalFormComponents.YesOrNoQuestion
                    name={EgenNringModalFormField.regnskapsfrerNrVennEllerFamilie}
                    legend={intlUtils(intl, 'inntektsinformasjon.egenNringModal.regnskapsfrerNrVennEllerFamilie')}
                />
            </Block>
            <Block
                padBottom="l"
                visible={visibility.isVisible(EgenNringModalFormField.regnskapsfrerNrVennEllerFamilie)}
            >
                <Veilederpanel fargetema="normal" svg={<VeilederNormal transparentBackground={true} />}>
                    <Normaltekst>
                        <FormattedMessage id="inntektsinformasjon.egenNringModal.regnskapsfrerVeileder" />
                    </Normaltekst>
                </Veilederpanel>
            </Block>
        </>
    );
};

export default Regnskapsfrer;
