import { Block, intlUtils } from '@navikt/fp-common';
import { QuestionVisibility } from '@navikt/sif-common-question-config/lib';
import React, { FunctionComponent } from 'react';
import { useIntl } from 'react-intl';
import { EgenNringModalFormComponents, EgenNringModalFormField } from '../egenNringModalFormConfig';
import { validateEgenNringOrgnr } from '../validation/egenNringValidation';

interface Props {
    visibility: QuestionVisibility<EgenNringModalFormField, undefined>;
}

const OrgnummerEllerLand: FunctionComponent<Props> = ({ visibility }) => {
    const intl = useIntl();

    return (
        <>
            <Block padBottom="l" visible={visibility.isVisible(EgenNringModalFormField.orgnr)}>
                <EgenNringModalFormComponents.NumberInput
                    name={EgenNringModalFormField.orgnr}
                    label={intlUtils(intl, 'inntektsinformasjon.egenNringModal.orgnr')}
                    validate={validateEgenNringOrgnr(intl)}
                />
            </Block>
            <Block padBottom="l" visible={visibility.isVisible(EgenNringModalFormField.land)}>
                <EgenNringModalFormComponents.CountrySelect
                    name={EgenNringModalFormField.land}
                    label={intlUtils(intl, 'inntektsinformasjon.egenNringModal.registrertILand')}
                />
            </Block>
        </>
    );
};

export default OrgnummerEllerLand;
