import { hasValue, Kjnn } from '@navikt/fp-common';
import { QuestionConfig, Questions } from '@navikt/sif-common-question-config/lib';
import { SkersituasjonFormData, SkersituasjonFormField } from './skersituasjonFormConfig';

interface SkersituasjonQuestionsPayload extends SkersituasjonFormData {
    skerKjnn: Kjnn;
}

const SkersituasjonFormConfig: QuestionConfig<SkersituasjonQuestionsPayload, SkersituasjonFormField> = {
    [SkersituasjonFormField.situasjon]: {
        isIncluded: () => true,
        isAnswered: ({ situasjon }) => hasValue(situasjon),
    },
    [SkersituasjonFormField.rolle]: {
        isIncluded: ({ skerKjnn }) => skerKjnn === 'K',
        isAnswered: ({ rolle }) => hasValue(rolle),
        visibilityFilter: ({ situasjon }) => hasValue(situasjon),
    },
};

const skersituasjonQuestionsConfig = Questions<SkersituasjonQuestionsPayload, SkersituasjonFormField>(
    SkersituasjonFormConfig
);

export default skersituasjonQuestionsConfig;
