import { FunctionComponent, useEffect, useState } from 'react';

import actionCreator from '../../app/context/action/actionCreator';
import { useForeldrepengesknadContext } from '../../app/context/hooks/useForeldrepengesknadContext';
import { ForeldrepengesknadContextState } from '../../app/context/ForeldrepengesknadContextConfig';
import { SkerinfoDTO } from '../../app/types/SkerinfoDTO';
import mapSkerinfoDTOToSkerinfo from '../../app/utils/mapSkerinfoDTO';

interface Props {
    children: any;
    sknad: ForeldrepengesknadContextState;
    skerinfo: SkerinfoDTO;
}

const ForeldrepengerStateMock: FunctionComponent<Props> = ({ children, sknad, skerinfo }) => {
    const [erFerdig, setFerdig] = useState(false);
    const { dispatch } = useForeldrepengesknadContext();

    useEffect(() => {
        if (!erFerdig) {
            Promise.all([
                dispatch(actionCreator.applyStoredState(sknad)),
                dispatch(actionCreator.setSkerinfo(mapSkerinfoDTOToSkerinfo(skerinfo))),
            ]).then(() => setFerdig(true));
        }
    }, [dispatch, sknad, skerinfo, erFerdig]);

    return erFerdig ? children : null;
};

export default ForeldrepengerStateMock;
