import React, { FunctionComponent, ReactNode } from 'react';

import SendSknadIkon, { OppsummeringIkonType } from './SendSknadIkon';
import { bemUtils, UtvidetInformasjon } from '@navikt/fp-common';

import './sknadSendtSectionHeader.less';
import { Element } from 'nav-frontend-typografi';

interface Props {
    title: ReactNode;
    info?: ReactNode;
    infoApneLabel?: ReactNode;
    type: OppsummeringIkonType;
    children?: ReactNode;
}

const SknadSendtSectionHeader: FunctionComponent<Props> = ({ title, info, type, infoApneLabel, children }) => {
    const bem = bemUtils('sknadSendtSectionHeader');

    return (
        <section className={bem.block}>
            <div className={bem.element('punkt')}>
                <div className={bem.element('ikon')}>
                    <SendSknadIkon type={type} />
                </div>
                <div className={bem.element('content')}>
                    <Element>{title}</Element>
                    <UtvidetInformasjon apneLabel={infoApneLabel}>{info}</UtvidetInformasjon>
                    {children}
                </div>
            </div>
        </section>
    );
};

export default SknadSendtSectionHeader;
