import React, { FunctionComponent } from 'react';
import { Block, intlUtils, Kjnn } from '@navikt/fp-common';
import { SkersituasjonFormComponents, SkersituasjonFormField } from '../skersituasjonFormConfig';
import { useIntl } from 'react-intl';

interface Props {
    kjnn: Kjnn;
}

const VelgRolle: FunctionComponent<Props> = ({ kjnn }) => {
    const intl = useIntl();

    if (kjnn === 'M') {
        return null;
    }

    return (
        <Block margin="xl">
            <SkersituasjonFormComponents.RadioPanelGroup
                name={SkersituasjonFormField.rolle}
                radios={[
                    {
                        label: intlUtils(intl, 'skersituasjon.radioButton.mor'),
                        value: 'mor',
                    },
                    {
                        label: intlUtils(intl, 'skersituasjon.radioButton.medmor'),
                        value: 'medmor',
                    },
                ]}
                useTwoColumns={true}
                legend={intlUtils(intl, 'skersituasjon.text.rolle')}
            />
        </Block>
    );
};

export default VelgRolle;
