import { erMyndig } from '@navikt/fp-common';
import Arbeidsforhold from 'app/types/Arbeidsforhold';
import Person, { RegistrertAnnenForelder, RegistrertBarn } from 'app/types/Person';
import { Skerinfo } from 'app/types/Skerinfo';
import { SkerinfoDTO, SkerinfoDTOArbeidsforhold, SkerinfoDTOBarn, SkerinfoDTOSker } from 'app/types/SkerinfoDTO';
import dayjs from 'dayjs';

const mapArbeidsforholdDTOToArbeidsforhold = (
    arbeidsforhold: SkerinfoDTOArbeidsforhold[] | undefined
): Arbeidsforhold[] => {
    if (!arbeidsforhold) {
        return [];
    }

    return arbeidsforhold.map((arbforhold) => {
        return {
            arbeidsgiverId: arbforhold.arbeidsgiverId,
            arbeidsgiverIdType: arbforhold.arbeidsgiverIdType,
            arbeidsgiverNavn: arbforhold.arbeidsgiverNavn,
            fom: dayjs(arbforhold.fom).toDate(),
            stillingsprosent: arbforhold.stillingsprosent,
            tom: arbforhold.tom ? dayjs(arbforhold.tom).toDate() : undefined,
        };
    });
};

const mapSkerinfoDTOSkerToPerson = (personDTO: SkerinfoDTOSker): Person => {
    return {
        erMyndig: erMyndig(personDTO.fdselsdato),
        etternavn: personDTO.etternavn,
        fornavn: personDTO.fornavn,
        fnr: personDTO.fnr,
        fdselsdato: dayjs(personDTO.fdselsdato).toDate(),
        ikkeNordiskEsLand: personDTO.ikkeNordiskEsLand,
        kjnn: personDTO.kjnn,
        bankkonto: personDTO.bankkonto,
    };
};

const mapSkerinfoDTOBarnToRegistrertBarn = (registrerteBarn: SkerinfoDTOBarn[] | undefined): RegistrertBarn[] => {
    if (!registrerteBarn) {
        return [];
    }

    return registrerteBarn.map((barn) => {
        const { annenForelder } = barn;
        const oppgittAnnenForelder: RegistrertAnnenForelder | undefined = annenForelder
            ? {
                  etternavn: annenForelder.etternavn,
                  fnr: annenForelder.fnr,
                  fornavn: annenForelder.fornavn,
                  fdselsdato: dayjs(annenForelder.fdselsdato).toDate(),
              }
            : undefined;

        return {
            etternavn: barn.etternavn,
            fnr: barn.fnr,
            fornavn: barn.fornavn,
            fdselsdato: dayjs(barn.fdselsdato).toDate(),
            kjnn: barn.kjnn,
            annenForelder: oppgittAnnenForelder,
        };
    });
};

const mapSkerinfoDTOToSkerinfo = (skerinfoDTO: SkerinfoDTO): Skerinfo => {
    const arbeidsforhold = mapArbeidsforholdDTOToArbeidsforhold(skerinfoDTO.sker.arbeidsforhold);
    const person = mapSkerinfoDTOSkerToPerson(skerinfoDTO.sker);
    const registrerteBarn = mapSkerinfoDTOBarnToRegistrertBarn(skerinfoDTO.sker.barn);

    return {
        arbeidsforhold,
        person,
        registrerteBarn,
    };
};

export default mapSkerinfoDTOToSkerinfo;
