import { useEffect, useState } from 'react';
import { useHistory } from 'react-router';
import Api from 'app/api/api';
import actionCreator from 'app/context/action/actionCreator';
import SknadRoutes from 'app/routes/routes';
import { useForeldrepengesknadContext } from 'app/context/hooks/useForeldrepengesknadContext';
import { ForeldrepengesknadContextAction } from 'app/context/action/actionCreator';

const useOnValidSubmit = <T>(
    submitHandler: (values: T) => ForeldrepengesknadContextAction[],
    nextRoute: SknadRoutes
) => {
    const { dispatch, state } = useForeldrepengesknadContext();
    const history = useHistory();
    const [harSubmitted, setSubmitted] = useState(false);

    useEffect(() => {
        if (harSubmitted) {
            Api.storeAppState(state);
            history.push(nextRoute);
        }
    }, [harSubmitted, history, nextRoute, state]);

    const setSubmitAndHandleSubmit = (values: T) => {
        const actions = submitHandler(values);
        const dispatchRouteChange =
            nextRoute === SknadRoutes.SKNAD_SENDT ? undefined : dispatch(actionCreator.updateCurrentRoute(nextRoute));
        Promise.all([dispatchRouteChange, ...actions.map((a) => dispatch(a))]).then(() => setSubmitted(true));
    };

    return setSubmitAndHandleSubmit;
};

export default useOnValidSubmit;
