import { Block, intlUtils } from '@navikt/fp-common';
import InfoOmSknaden from 'app/components/info-eksisterende-sak/InfoOmSknaden';
import actionCreator from 'app/context/action/actionCreator';
import { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import { FarMedmorFrstegangssknadMedAnnenPartUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import SknadRoutes from 'app/routes/routes';
import { EksisterendeSak } from 'app/types/EksisterendeSak';
import { Forelder } from 'app/types/Forelder';
import { TilgjengeligeStnadskontoerDTO } from 'app/types/TilgjengeligeStnadskontoerDTO';
import { getErMorUfr } from 'app/utils/annenForelderUtils';
import { getFamiliehendelsedato } from 'app/utils/barnUtils';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import useSknad from 'app/utils/hooks/useSknad';
import useUttaksplanInfo from 'app/utils/hooks/useUttaksplanInfo';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';
import { getValgtStnadskontoFor80Og100Prosent } from 'app/utils/stnadskontoUtils';
import { Hovedknapp } from 'nav-frontend-knapper';
import React, { FunctionComponent } from 'react';
import { useIntl } from 'react-intl';
import { isInfoPeriode } from 'uttaksplan/types/Periode';
import FarMedmorsFrsteDag from '../sprsml/FarMedmorsFrsteDag';
import {
    FarMedmorFrstegangssknadMedAnnenPartFormComponents,
    FarMedmorFrstegangssknadMedAnnenPartFormData,
    FarMedmorFrstegangssknadMedAnnenPartFormField,
} from './farMedmorFrstegangssknadMedAnnenPartFormConfig';
import { farMedmorFrstegangssknadMedAnnenPartQuestionsConfig } from './farMedmorFrstegangssknadMedAnnenPartQuestionsConfig';
import { getFarMedmorFrstegangssknadMedAnnenPartInitialValues } from './farMedmorFrstegangssknadMedAnnenPartUtils';

interface Props {
    tilgjengeligeStnadskontoer100DTO: TilgjengeligeStnadskontoerDTO;
    tilgjengeligeStnadskontoer80DTO: TilgjengeligeStnadskontoerDTO;
    eksisterendeSakAnnenPart: EksisterendeSak | undefined;
}

const FarMedmorFrstegangssknadMedAnnenPart: FunctionComponent<Props> = ({
    tilgjengeligeStnadskontoer100DTO,
    tilgjengeligeStnadskontoer80DTO,
    eksisterendeSakAnnenPart,
}) => {
    const sknad = useSknad();
    const intl = useIntl();
    const { barn, skersituasjon, annenForelder } = sknad;
    const erFarEllerMedmor = isFarEllerMedmor(skersituasjon.rolle);
    const lagretUttaksplanInfo = useUttaksplanInfo<FarMedmorFrstegangssknadMedAnnenPartUttaksplanInfo>();

    const onValidSubmitHandler = (values: Partial<FarMedmorFrstegangssknadMedAnnenPartFormData>) => {
        const uttaksplanInfo: FarMedmorFrstegangssknadMedAnnenPartUttaksplanInfo = {
            permisjonStartdato: values.permisjonStartdato!,
        };

        return [actionCreator.setUttaksplanInfo(uttaksplanInfo), actionCreator.setDekningsgrad(grunnlag.dekningsgrad)];
    };

    const onValidSubmit = useOnValidSubmit(onValidSubmitHandler, SknadRoutes.UTTAKSPLAN);

    if (!eksisterendeSakAnnenPart || !erFarEllerMedmor) {
        return null;
    }

    const familiehendelsedato = getFamiliehendelsedato(barn);
    const erMorUfr = getErMorUfr(annenForelder, erFarEllerMedmor);
    const navnMor = isAnnenForelderOppgitt(annenForelder) ? annenForelder.fornavn : '';
    const { grunnlag, uttaksplan } = eksisterendeSakAnnenPart;
    const morsPerioder = uttaksplan.filter((p) => isInfoPeriode(p) && p.forelder === Forelder.mor);
    const morsSisteDag = morsPerioder.reverse()[0].tidsperiode.tom;

    const tilgjengeligeStnadskontoer = getValgtStnadskontoFor80Og100Prosent(
        tilgjengeligeStnadskontoer80DTO,
        tilgjengeligeStnadskontoer100DTO,
        familiehendelsedato,
        erMorUfr
    );

    return (
        <FarMedmorFrstegangssknadMedAnnenPartFormComponents.FormikWrapper
            initialValues={getFarMedmorFrstegangssknadMedAnnenPartInitialValues(lagretUttaksplanInfo)}
            onSubmit={onValidSubmit}
            renderForm={({ values: formValues, setFieldValue }) => {
                const visibility = farMedmorFrstegangssknadMedAnnenPartQuestionsConfig.getVisbility(formValues);
                const valgtMengdeStnadskonto = tilgjengeligeStnadskontoer[grunnlag.dekningsgrad];

                return (
                    <FarMedmorFrstegangssknadMedAnnenPartFormComponents.Form
                        includeButtons={false}
                        includeValidationSummary={true}
                    >
                        <Block padBottom="l">
                            <InfoOmSknaden
                                eksisterendeSak={eksisterendeSakAnnenPart}
                                erIUttaksplanenSteg={false}
                                tilgjengeligeStnadskontoer={valgtMengdeStnadskonto}
                            />
                        </Block>
                        <Block padBottom="l">
                            <FarMedmorsFrsteDag
                                FormComponents={FarMedmorFrstegangssknadMedAnnenPartFormComponents}
                                fieldName={FarMedmorFrstegangssknadMedAnnenPartFormField.permisjonStartdato}
                                familiehendelsesdato={familiehendelsedato}
                                setFieldValue={setFieldValue}
                                morsSisteDag={morsSisteDag}
                                navnMor={navnMor}
                            />
                        </Block>
                        <Block visible={visibility.areAllQuestionsAnswered()} textAlignCenter={true}>
                            <Hovedknapp>{intlUtils(intl, 'sknad.gVidere')}</Hovedknapp>
                        </Block>
                    </FarMedmorFrstegangssknadMedAnnenPartFormComponents.Form>
                );
            }}
        />
    );
};

export default FarMedmorFrstegangssknadMedAnnenPart;
