import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';

export const getVelgbareStnadskontotyper = (stnadskontoTyper: TilgjengeligStnadskonto[]): StnadskontoType[] =>
    stnadskontoTyper
        .filter(
            (kontoType) =>
                kontoType.konto === StnadskontoType.Flerbarnsdager ||
                kontoType.konto === StnadskontoType.Fellesperiode ||
                kontoType.konto === StnadskontoType.Fedrekvote ||
                kontoType.konto === StnadskontoType.Mdrekvote ||
                kontoType.konto === StnadskontoType.Foreldrepenger ||
                kontoType.konto === StnadskontoType.AktivitetsfriKvote
        )
        .map((kontoType) => kontoType.konto);

export const getStnadskontoSortOrder = (konto: StnadskontoType): number => stnadskontoSortOrder[konto];

export const stnadskontoSortOrder = {
    [StnadskontoType.ForeldrepengerFrFdsel]: 1,
    [StnadskontoType.Mdrekvote]: 2,
    [StnadskontoType.Fedrekvote]: 3,
    [StnadskontoType.Fellesperiode]: 4,
    [StnadskontoType.Foreldrepenger]: 5,
    [StnadskontoType.Flerbarnsdager]: 6,
    [StnadskontoType.AktivitetsfriKvote]: 7,
};

export const getAntallUker = (kontoer: TilgjengeligStnadskonto[]): number => {
    return kontoer
        .filter((konto: TilgjengeligStnadskonto) => konto.konto !== StnadskontoType.Flerbarnsdager)
        .reduce((sum: number, konto: TilgjengeligStnadskonto) => sum + konto.dager / 5, 0);
};

export const getAntallUkerMdrekvote = (kontoer: TilgjengeligStnadskonto[]): number => {
    return kontoer
        .filter((konto: TilgjengeligStnadskonto) => konto.konto === StnadskontoType.Mdrekvote)
        .reduce((sum: number, konto: TilgjengeligStnadskonto) => sum + konto.dager / 5, 0);
};

export const getAntallUkerFedrekvote = (kontoer: TilgjengeligStnadskonto[]): number => {
    return kontoer
        .filter((konto: TilgjengeligStnadskonto) => konto.konto === StnadskontoType.Fedrekvote)
        .reduce((sum: number, konto: TilgjengeligStnadskonto) => sum + konto.dager / 5, 0);
};

export const getAntallUkerFellesperiode = (kontoer: TilgjengeligStnadskonto[]): number => {
    return kontoer
        .filter((konto: TilgjengeligStnadskonto) => konto.konto === StnadskontoType.Fellesperiode)
        .reduce((sum: number, konto: TilgjengeligStnadskonto) => sum + konto.dager / 5, 0);
};
