import { YesOrNo } from '@navikt/sif-common-formik/lib';
import { FarMedmorAleneomsorgFdselUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import { Dekningsgrad } from 'app/types/Dekningsgrad';
import dayjs from 'dayjs';
import {
    FarMedmorAleneomsorgFdselFormData,
    FarMedmorAleneomsorgFdselFormField,
} from './farMedmorAleneomsorgFdselFormConfig';

const initialFarMedmorAleneomsorgFdselValues: FarMedmorAleneomsorgFdselFormData = {
    [FarMedmorAleneomsorgFdselFormField.dekningsgrad]: '',
    [FarMedmorAleneomsorgFdselFormField.startPOmsorgsovertakelse]: YesOrNo.UNANSWERED,
    [FarMedmorAleneomsorgFdselFormField.startdatoUttak]: '',
};

export const mapFarMedmorAleneomsorgFdselFormToState = (
    values: Partial<FarMedmorAleneomsorgFdselFormData>,
    familiehendelsesdato: string
): FarMedmorAleneomsorgFdselUttaksplanInfo => {
    return {
        fellesperiodeukerMor: undefined,
        startdatoUttak:
            values.startPOmsorgsovertakelse === YesOrNo.YES ? familiehendelsesdato : values.startdatoUttak!,
    };
};

export const getInitialFarMedmorAleneomsorgFdselValues = (
    lagretUttaksplanInfo: FarMedmorAleneomsorgFdselUttaksplanInfo | undefined,
    familiehendelsesdato: string,
    dekningsgrad: Dekningsgrad
): FarMedmorAleneomsorgFdselFormData => {
    if (lagretUttaksplanInfo) {
        const startetPOmsorgsovertakelse = dayjs(lagretUttaksplanInfo.startdatoUttak).isSame(
            dayjs(familiehendelsesdato)
        );

        return {
            dekningsgrad,
            startPOmsorgsovertakelse: startetPOmsorgsovertakelse ? YesOrNo.YES : YesOrNo.NO,
            startdatoUttak: startetPOmsorgsovertakelse ? '' : lagretUttaksplanInfo.startdatoUttak,
        };
    }

    return initialFarMedmorAleneomsorgFdselValues;
};
