import { Forelder } from 'app/types/Forelder';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';
import { TilgjengeligeDager } from './../../../../types/TilgjengeligeDager';

export const summerAntallDagerIKontoer = (kontoer: TilgjengeligStnadskonto[]): number => {
    return kontoer.reduce((dager, konto) => konto.dager + dager, 0);
};

const getMorsStnadskontoer = (kontoer: TilgjengeligStnadskonto[]): TilgjengeligStnadskonto[] =>
    kontoer.filter((konto) => konto.konto === StnadskontoType.Mdrekvote);

const getFarsStnadskontoer = (kontoer: TilgjengeligStnadskonto[]): TilgjengeligStnadskonto[] =>
    kontoer.filter((konto) => konto.konto === StnadskontoType.Fedrekvote);

const getForeldrepengeKontoer = (kontoer: TilgjengeligStnadskonto[]): TilgjengeligStnadskonto[] =>
    kontoer.filter((konto) => konto.konto === StnadskontoType.Foreldrepenger);

const getFlerbarnskonto = (kontoer: TilgjengeligStnadskonto[]): TilgjengeligStnadskonto[] =>
    kontoer.filter((konto) => konto.konto === StnadskontoType.Flerbarnsdager);

const getFellesStnadskontoer = (kontoer: TilgjengeligStnadskonto[]): TilgjengeligStnadskonto[] =>
    kontoer.filter(
        (konto) => konto.konto === StnadskontoType.Fellesperiode || konto.konto === StnadskontoType.Flerbarnsdager
    );

const kontoErFrTermin = (konto: TilgjengeligStnadskonto): boolean => {
    return konto.konto === StnadskontoType.ForeldrepengerFrFdsel;
};

const kontoErEtterTermin = (konto: TilgjengeligStnadskonto): boolean => {
    return konto.konto !== StnadskontoType.ForeldrepengerFrFdsel;
};

export const getTilgjengeligeDager = (
    kontoer: TilgjengeligStnadskonto[],
    erDeltUttak: boolean,
    forelderVedAleneomsorg: Forelder | undefined
): TilgjengeligeDager => {
    const kontoerEtterTermin = kontoer.filter(kontoErEtterTermin);

    const dagerTotalt = summerAntallDagerIKontoer(kontoer);
    const dagerForeldrepengerFrFdsel = summerAntallDagerIKontoer(kontoer.filter(kontoErFrTermin));
    const dagerEtterTermin = summerAntallDagerIKontoer(kontoerEtterTermin);
    const dagerForeldrepenger = summerAntallDagerIKontoer(getForeldrepengeKontoer(kontoerEtterTermin));
    const dagerMor = summerAntallDagerIKontoer(getMorsStnadskontoer(kontoerEtterTermin));
    const dagerFar = summerAntallDagerIKontoer(getFarsStnadskontoer(kontoerEtterTermin));
    const dagerFelles = summerAntallDagerIKontoer(getFellesStnadskontoer(kontoerEtterTermin));
    const flerbarnsdager = summerAntallDagerIKontoer(getFlerbarnskonto(kontoerEtterTermin));

    const dagerKunTilMor = erDeltUttak === false && forelderVedAleneomsorg === Forelder.mor;
    const dagerKunTilFar = erDeltUttak === false && forelderVedAleneomsorg === Forelder.farMedmor;

    const maksDagerFar = dagerKunTilFar ? dagerForeldrepenger : dagerFar + dagerFelles;
    const maksDagerMor = dagerKunTilMor ? dagerForeldrepenger + dagerForeldrepengerFrFdsel : dagerMor + dagerFelles;

    return {
        dagerTotalt,
        dagerForeldrepengerFrFdsel,
        dagerEtterTermin,
        dagerForeldrepenger,
        dagerMor,
        dagerFarMedmor: dagerFar,
        dagerFelles,
        flerbarnsdager,
        maksDagerFar,
        maksDagerMor,
        stnadskontoer: kontoer,
    };
};
