import { TilgjengeligeStnadskontoerDTO } from 'app/types/TilgjengeligeStnadskontoerDTO';
import React, { FunctionComponent } from 'react';
import FarMedmorFdselFrsteganggsknadBeggeHarRett from './scenarios/far-medmor-fdsel-begge-har-rett/FarMedmorFdselBeggeHarRett';
import MorFdsel from './scenarios/mor-fodsel/MorFdsel';
import MorFarAdopsjon from './scenarios/mor-far-adopsjon/MorFarAdopsjon';
import FarMedmorAleneomsorgFdselAdopsjon from './scenarios/far-medmor-aleneomsorg-fdsel/FarMedmorAleneomsorgFdsel';
import FarMedmorFdselOgMorHarIkkeRett from './scenarios/far-medmor-fdsel-og-mor-har-ikke-rett/FarMedmorFdselOgMorHarIkkeRett';
import { EksisterendeSak } from 'app/types/EksisterendeSak';
import FarMedmorFrstegangssknadMedAnnenPart from './scenarios/farMedmor-frstegangssknad-med-annen-part/FarMedmorFrstegangssknadMedAnnenPart';

interface Props {
    tilgjengeligeStnadskontoer100DTO: TilgjengeligeStnadskontoerDTO;
    tilgjengeligeStnadskontoer80DTO: TilgjengeligeStnadskontoerDTO;
    eksisterendeSakAnnenPart: EksisterendeSak | undefined;
}

const UttaksplanInfoScenarios: FunctionComponent<Props> = ({
    tilgjengeligeStnadskontoer100DTO,
    tilgjengeligeStnadskontoer80DTO,
    eksisterendeSakAnnenPart,
}) => {
    return (
        <>
            <FarMedmorFdselFrsteganggsknadBeggeHarRett
                tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
                eksisterendeSakAnnenPart={eksisterendeSakAnnenPart}
            />
            <MorFdsel
                tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
            />
            <MorFarAdopsjon
                tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
            />
            <FarMedmorAleneomsorgFdselAdopsjon
                tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
            />
            <FarMedmorFdselOgMorHarIkkeRett
                tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
            />
            <FarMedmorFrstegangssknadMedAnnenPart
                tilgjengeligeStnadskontoer100DTO={tilgjengeligeStnadskontoer100DTO}
                tilgjengeligeStnadskontoer80DTO={tilgjengeligeStnadskontoer80DTO}
                eksisterendeSakAnnenPart={eksisterendeSakAnnenPart}
            />
        </>
    );
};

export default UttaksplanInfoScenarios;
