import React, { FunctionComponent } from 'react';
import Modal from 'nav-frontend-modal';
import { bemUtils, Block, intlUtils, UtvidetInformasjon } from '@navikt/fp-common';
import { Normaltekst, Undertittel } from 'nav-frontend-typografi';
import { FormattedMessage, useIntl } from 'react-intl';
import {
    EgenNringModalFormComponents,
    EgenNringModalFormData,
    EgenNringModalFormField,
} from './egenNringModalFormConfig';
import {
    cleanupEgenNringForm,
    getInitialEgenNringModalValues,
    mapEgenNringModalFormValuesToState,
} from './egenNringModalFormUtils';
import { Nring, Nringstype } from 'app/context/types/Nring';
import egenNringModalQuestionsConfig from './egenNringModalQuestionsConfig';
import OrgnummerEllerLand from './components/OrgnummerEllerLand';
import Regnskapsfrer from './components/Regnskapsfrer';
import { Hovedknapp } from 'nav-frontend-knapper';

import './egenNringModal.less';
import {
    validateEgenNringFom,
    validateEgenNringTom,
    validateEgenNringForklaringTilEndring,
    validateEgenNringEndringAvInntektsDato,
    validateEgenNringYrkesAktivDatoDato,
    validateNumber,
} from './validation/egenNringValidation';
import dayjs from 'dayjs';

interface Props {
    isOpen: boolean;
    title: string;
    onRequestClose: () => void;
    selectedNring?: Nring;
    addNring: (nring: Nring) => void;
    editNring: (nring: Nring) => void;
}

const EgenNringModal: FunctionComponent<Props> = ({
    isOpen,
    title,
    onRequestClose,
    selectedNring,
    addNring,
    editNring,
}) => {
    const intl = useIntl();
    const bem = bemUtils('egenNringModal');

    const onValidSubmit = (values: Partial<EgenNringModalFormData>) => {
        if (!selectedNring) {
            addNring(mapEgenNringModalFormValuesToState(values));
        } else {
            editNring(mapEgenNringModalFormValuesToState(values));
        }
        onRequestClose();
    };

    return (
        <Modal
            isOpen={isOpen}
            contentLabel={title}
            onRequestClose={onRequestClose}
            closeButton={true}
            shouldCloseOnOverlayClick={false}
            className={bem.block}
        >
            <EgenNringModalFormComponents.FormikWrapper
                initialValues={getInitialEgenNringModalValues(selectedNring)}
                onSubmit={(values: Partial<EgenNringModalFormData>) => onValidSubmit(values)}
                renderForm={({ values: formValues }) => {
                    const visibility = egenNringModalQuestionsConfig.getVisbility(formValues);

                    return (
                        <EgenNringModalFormComponents.Form
                            includeButtons={false}
                            cleanup={(values) => cleanupEgenNringForm(values, visibility)}
                            includeValidationSummary={true}
                        >
                            <Block padBottom="l">
                                <Undertittel className={bem.element('tittel')}>
                                    <FormattedMessage id="inntektsinformasjon.egenNringModal.tittel" />
                                </Undertittel>
                            </Block>
                            <Block padBottom="l" visible={visibility.isVisible(EgenNringModalFormField.typer)}>
                                <EgenNringModalFormComponents.CheckboxPanelGroup
                                    name={EgenNringModalFormField.typer}
                                    legend={intlUtils(intl, 'inntektsinformasjon.egenNringModal.nringstype')}
                                    checkboxes={[
                                        {
                                            label: intlUtils(
                                                intl,
                                                'inntektsinformasjon.egenNringModal.nringstype.dagmamma'
                                            ),
                                            value: Nringstype.DAGMAMMA,
                                        },
                                        {
                                            label: intlUtils(
                                                intl,
                                                'inntektsinformasjon.egenNringModal.nringstype.fiske'
                                            ),
                                            value: Nringstype.FISKER,
                                        },
                                        {
                                            label: intlUtils(
                                                intl,
                                                'inntektsinformasjon.egenNringModal.nringstype.jordbrukSkogbruk'
                                            ),
                                            value: Nringstype.JORDBRUK,
                                        },
                                        {
                                            label: intlUtils(
                                                intl,
                                                'inntektsinformasjon.egenNringModal.nringstype.annen'
                                            ),
                                            value: Nringstype.ANNET,
                                        },
                                    ]}
                                />
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(EgenNringModalFormField.navnPNringen)}
                            >
                                <EgenNringModalFormComponents.Input
                                    name={EgenNringModalFormField.navnPNringen}
                                    label={intlUtils(intl, 'inntektsinformasjon.egenNringModal.navnPNring')}
                                />
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(EgenNringModalFormField.registrertINorge)}
                            >
                                <EgenNringModalFormComponents.YesOrNoQuestion
                                    name={EgenNringModalFormField.registrertINorge}
                                    legend={intlUtils(
                                        intl,
                                        'inntektsinformasjon.egenNringModal.erNringenRegistrertINorge',
                                        {
                                            navnPNringen: formValues.navnPNringen,
                                        }
                                    )}
                                />
                            </Block>
                            <OrgnummerEllerLand visibility={visibility} />
                            <Block padBottom="l" visible={visibility.isVisible(EgenNringModalFormField.fom)}>
                                <EgenNringModalFormComponents.DatePicker
                                    name={EgenNringModalFormField.fom}
                                    label={intlUtils(intl, 'inntektsinformasjon.egenNringModal.startetNring.fom', {
                                        navnPNringen: formValues.navnPNringen,
                                    })}
                                    placeholder="dd.mm."
                                    fullscreenOverlay={true}
                                    showYearSelector={true}
                                    validate={validateEgenNringFom(intl, formValues.tom)}
                                    maxDate={dayjs().toDate()}
                                />
                            </Block>
                            <Block padBottom="l" visible={visibility.isVisible(EgenNringModalFormField.pgende)}>
                                <EgenNringModalFormComponents.YesOrNoQuestion
                                    name={EgenNringModalFormField.pgende}
                                    legend={intlUtils(
                                        intl,
                                        'inntektsinformasjon.egenNringModal.startetNring.pgende',
                                        {
                                            navnPNringen: formValues.navnPNringen,
                                        }
                                    )}
                                />
                            </Block>
                            <Block padBottom="l" visible={visibility.isVisible(EgenNringModalFormField.tom)}>
                                <EgenNringModalFormComponents.DatePicker
                                    name={EgenNringModalFormField.tom}
                                    label={intlUtils(intl, 'inntektsinformasjon.egenNringModal.startetNring.tom', {
                                        navnPNringen: formValues.navnPNringen,
                                    })}
                                    placeholder="dd.mm."
                                    fullscreenOverlay={true}
                                    showYearSelector={true}
                                    validate={validateEgenNringTom(intl, formValues.fom)}
                                    maxDate={dayjs().toDate()}
                                    minDate={dayjs(formValues.fom).toDate()}
                                />
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(EgenNringModalFormField.nringsresultat)}
                            >
                                <EgenNringModalFormComponents.NumberInput
                                    name={EgenNringModalFormField.nringsresultat}
                                    label={intlUtils(intl, 'inntektsinformasjon.egenNringModal.nringsinntekt')}
                                    description={
                                        <UtvidetInformasjon
                                            apneLabel={intlUtils(
                                                intl,
                                                'inntektsinformasjon.egenNringModal.nringsinntekt.info.apneLabel'
                                            )}
                                        >
                                            <Normaltekst>
                                                <FormattedMessage id="inntektsinformasjon.egenNringModal.nringsinntekt.info" />
                                            </Normaltekst>
                                        </UtvidetInformasjon>
                                    }
                                    validate={validateNumber(
                                        intl,
                                        'valideringsfeil.inntektsinformasjon.nringsinntekt.ugyldigFormat'
                                    )}
                                />
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(
                                    EgenNringModalFormField.harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene
                                )}
                            >
                                <EgenNringModalFormComponents.YesOrNoQuestion
                                    name={
                                        EgenNringModalFormField.harBlittYrkesaktivILpetAvDeTreSisteFerdigliknederene
                                    }
                                    legend={intlUtils(
                                        intl,
                                        'inntektsinformasjon.egenNringModal.blittYrkesaktivSiste3r'
                                    )}
                                    description={
                                        <UtvidetInformasjon
                                            apneLabel={intlUtils(
                                                intl,
                                                'inntektsinformasjon.egenNringModal.blittYrkesaktivSiste3r.info.apneLabel'
                                            )}
                                        >
                                            <Normaltekst>
                                                <FormattedMessage id="inntektsinformasjon.egenNringModal.blittYrkesaktivSiste3r.info" />
                                            </Normaltekst>
                                        </UtvidetInformasjon>
                                    }
                                />
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(EgenNringModalFormField.yrkesAktivDato)}
                            >
                                <EgenNringModalFormComponents.DatePicker
                                    name={EgenNringModalFormField.yrkesAktivDato}
                                    label={intlUtils(intl, 'inntektsinformasjon.egenNringModal.yrkesaktivDato')}
                                    placeholder="dd.mm."
                                    fullscreenOverlay={true}
                                    showYearSelector={true}
                                    validate={validateEgenNringYrkesAktivDatoDato(intl)}
                                    maxDate={dayjs().toDate()}
                                />
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(
                                    EgenNringModalFormField.hattVarigEndringAvNringsinntektSiste4Kalenderr
                                )}
                            >
                                <EgenNringModalFormComponents.YesOrNoQuestion
                                    name={EgenNringModalFormField.hattVarigEndringAvNringsinntektSiste4Kalenderr}
                                    legend={intlUtils(
                                        intl,
                                        'inntektsinformasjon.egenNringModal.varigEndringAvNringsinntekt'
                                    )}
                                />
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(EgenNringModalFormField.datoForEndring)}
                            >
                                <EgenNringModalFormComponents.DatePicker
                                    name={EgenNringModalFormField.datoForEndring}
                                    label={intlUtils(
                                        intl,
                                        'inntektsinformasjon.egenNringModal.varigEndringAvNringsinntektDato'
                                    )}
                                    placeholder="dd.mm."
                                    fullscreenOverlay={true}
                                    showYearSelector={true}
                                    validate={validateEgenNringEndringAvInntektsDato(intl)}
                                    maxDate={dayjs().toDate()}
                                />
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(EgenNringModalFormField.inntektEtterEndring)}
                            >
                                <EgenNringModalFormComponents.NumberInput
                                    name={EgenNringModalFormField.inntektEtterEndring}
                                    label={intlUtils(intl, 'inntektsinformasjon.egenNringModal.inntektEtterEndring')}
                                    validate={validateNumber(
                                        intl,
                                        'valideringsfeil.inntektsinformasjon.varigEndringAvInntekt.ugyldigFormat'
                                    )}
                                />
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(EgenNringModalFormField.forklaringEndring)}
                            >
                                <EgenNringModalFormComponents.Textarea
                                    name={EgenNringModalFormField.forklaringEndring}
                                    label={intlUtils(
                                        intl,
                                        'inntektsinformasjon.egenNringModal.varigEndringAvNringsinntektForklaring'
                                    )}
                                    maxLength={1000}
                                    validate={validateEgenNringForklaringTilEndring(intl)}
                                />
                            </Block>
                            <Regnskapsfrer visibility={visibility} />
                            <Block visible={visibility.areAllQuestionsAnswered()} textAlignCenter={true}>
                                <Hovedknapp>{intlUtils(intl, 'sknad.gVidere')}</Hovedknapp>
                            </Block>
                        </EgenNringModalFormComponents.Form>
                    );
                }}
            />
        </Modal>
    );
};

export default EgenNringModal;
