import { Block, intlUtils, Step } from '@navikt/fp-common';
import actionCreator from 'app/context/action/actionCreator';
import SknadRoutes from 'app/routes/routes';
import { Hovedknapp } from 'nav-frontend-knapper';
import React from 'react';
import { useIntl } from 'react-intl';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import useSknad from 'app/utils/hooks/useSknad';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import useAvbrytSknad from 'app/utils/hooks/useAvbrytSknad';
import stepConfig, { getPreviousStepHref } from '../stepsConfig';
import AdopsjonAnnetBarn from './components/AdopsjonAnnetBarn';
import AdopsjonEktefellesBarn from './components/AdopsjonEktefellesBarn';
import BarnFdtEllerAdoptert from './components/BarnFdtEllerAdoptert';
import Fdsel from './components/Fdsel';
import Termin from './components/Termin';
import { OmBarnetFormComponents, OmBarnetFormData } from './omBarnetFormConfig';
import omBarnetQuestionsConfig from './omBarnetQuestionsConfig';
import { getOmBarnetInitialValues, mapOmBarnetFormDataToState } from './omBarnetUtils';
import RegistrertBarn from './components/RegistrertBarn';

const OmBarnet: React.FunctionComponent = () => {
    const intl = useIntl();
    const { skersituasjon, barn } = useSknad();
    const { arbeidsforhold, registrerteBarn } = useSkerinfo();

    const onValidSubmitHandler = (values: Partial<OmBarnetFormData>) => {
        const barn = mapOmBarnetFormDataToState(values, registrerteBarn);
        return [actionCreator.setOmBarnet(barn)];
    };

    const onValidSubmit = useOnValidSubmit(onValidSubmitHandler, SknadRoutes.ANNEN_FORELDER);
    const onAvbrytSknad = useAvbrytSknad();

    return (
        <OmBarnetFormComponents.FormikWrapper
            initialValues={getOmBarnetInitialValues(barn, registrerteBarn)}
            onSubmit={onValidSubmit}
            renderForm={({ values: formValues, setFieldValue }) => {
                const visibility = omBarnetQuestionsConfig.getVisbility({
                    ...formValues,
                    arbeidsforhold,
                    situasjon: skersituasjon.situasjon,
                    rolle: skersituasjon.rolle,
                    registrerteBarn,
                });

                return (
                    <Step
                        bannerTitle={intlUtils(intl, 'sknad.pageheading')}
                        backLinkHref={getPreviousStepHref('omBarnet')}
                        activeStepId="omBarnet"
                        pageTitle={intlUtils(intl, 'sknad.omBarnet')}
                        stepTitle={intlUtils(intl, 'sknad.omBarnet')}
                        onCancel={onAvbrytSknad}
                        onContinueLater={() => null}
                        steps={stepConfig}
                        kompakt={true}
                    >
                        <OmBarnetFormComponents.Form includeButtons={false}>
                            <RegistrertBarn
                                registrerteBarn={registrerteBarn}
                                visibility={visibility}
                                formValues={formValues}
                                setFieldValue={setFieldValue}
                            />
                            <BarnFdtEllerAdoptert visibility={visibility} />
                            <AdopsjonAnnetBarn
                                skersituasjon={skersituasjon}
                                formValues={formValues}
                                visibility={visibility}
                            />
                            <AdopsjonEktefellesBarn
                                skersituasjon={skersituasjon}
                                formValues={formValues}
                                visibility={visibility}
                            />
                            <Termin skersituasjon={skersituasjon} formValues={formValues} visibility={visibility} />
                            <Fdsel skersituasjon={skersituasjon} formValues={formValues} visibility={visibility} />
                            <Block visible={visibility.areAllQuestionsAnswered()} textAlignCenter={true}>
                                <Hovedknapp>{intlUtils(intl, 'sknad.gVidere')}</Hovedknapp>
                            </Block>
                        </OmBarnetFormComponents.Form>
                    </Step>
                );
            }}
        />
    );
};

export default OmBarnet;
