import React from 'react';
import { Story } from '@storybook/react';
import { Kjnn } from '@navikt/fp-common';
import skerinfo from './testdata/skerinfo.json';
import context from './testdata/context.json';
import { SkerinfoDTO } from 'app/types/SkerinfoDTO';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import Skersituasjon from 'app/steps/skersituasjon/Skersituasjon';
import withIntlProvider from '../../../decorators/withIntl';
import withRouter from '../../../decorators/withRouter';
import withForeldrepengersknadContext from '../../../decorators/withForeldrepengersknadContext';
import ForeldrepengerStateMock from '../../../utils/ForeldrepengerStateMock';

export default {
    title: 'steps/Skersituasjon',
    component: Skersituasjon,
    decorators: [withRouter, withIntlProvider, withForeldrepengersknadContext],
};

interface Props {
    context: ForeldrepengesknadContextState;
    skerinfo: SkerinfoDTO;
    kjnn: Kjnn;
}

const Template: Story<Props> = ({ context, skerinfo, kjnn }) => {
    return (
        <ForeldrepengerStateMock sknad={context} skerinfo={skerinfo}>
            <Skersituasjon kjnn={kjnn} />
        </ForeldrepengerStateMock>
    );
};

export const Default = Template.bind({});;
Default.args = {
    context,
    skerinfo,
    kjnn: 'K',
};

export const Far = Template.bind({});
Far.args = {
    context,
    skerinfo: {
        sker: {
            ...skerinfo,
            kjnn: 'M',
        },
    } as SkerinfoDTO,
    kjnn: 'M',
};



