import React from 'react';
import { Story } from '@storybook/react';
import MockAdapter from 'axios-mock-adapter/types';

import UttaksplanInfo from 'app/steps/uttaksplan-info/UttaksplanInfo';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import { SkerinfoDTO } from 'app/types/SkerinfoDTO';
import skerinfoMorSker from './testdata/skerinfoMorSker.json';
import contextMorSker from './testdata/contextMorSker.json';
import skerinfoFarSker from './testdata/skerinfoFarSker.json';
import contextFarSker from './testdata/contextFarSker.json';
import stnadskonto100 from './../testdata/stnadskonto100.json';
import stnadskonto80 from './../testdata/stnadskonto80.json';
import stnadskontoDeltUttak80 from './../testdata/stnadskontoDeltUttak80.json';
import stnadskontoDeltUttak100 from './../testdata/stnadskontoDeltUttak100.json';
import withIntl from '../../../../decorators/withIntl';
import withRouter from '../../../../decorators/withRouter';
import withForeldrepengersknadContext from '../../../../decorators/withForeldrepengersknadContext';
import AxiosMock from '../../../../utils/AxiosMock';
import ForeldrepengerStateMock from '../../../../utils/ForeldrepengerStateMock';
import UttaksplanInfoTestData from '../uttaksplanInfoTestData';

const UTTAKSPLAN_ANNEN_URL = '/innsyn/uttaksplanannen';
const STNADSKONTO_URL = '/uttak-url/konto';

export default {
    title: 'steps/uttaksplan-info/MorFarAdopsjon',
    component: UttaksplanInfo,
    decorators: [withRouter, withIntl, withForeldrepengersknadContext],
};

const Template: Story<UttaksplanInfoTestData> = (args) => {
    const restMock = (apiMock: MockAdapter) => {
        apiMock.onGet(UTTAKSPLAN_ANNEN_URL).replyOnce(200, {});
        apiMock.onGet(STNADSKONTO_URL).replyOnce(200, args.stnadskonto100);
        apiMock.onGet(STNADSKONTO_URL).replyOnce(200, args.stnadskonto80);
    };
    return (
        <AxiosMock mock={restMock}>
            <ForeldrepengerStateMock
                sknad={args.context as ForeldrepengesknadContextState}
                skerinfo={args.skerinfo as SkerinfoDTO}
            >
                <UttaksplanInfo />
            </ForeldrepengerStateMock>
        </AxiosMock>
    );
};

export const UttaksplanMedAleneomsorg = Template.bind({});;
UttaksplanMedAleneomsorg.args = {
    stnadskonto100,
    stnadskonto80,
    context: contextMorSker,
    skerinfo: skerinfoMorSker,
};

export const UttaksplanMedDeltUttakDerMorSker = Template.bind({});;
UttaksplanMedDeltUttakDerMorSker.args = {
    stnadskonto100: stnadskontoDeltUttak100,
    stnadskonto80: stnadskontoDeltUttak80,
    context: {
        ...contextMorSker,
        sknad: {
            ...contextMorSker.sknad,
            sker: {
                ...contextMorSker.sknad.sker,
                erAleneOmOmsorg: false,
            },
            annenForelder: {
                fornavn: 'Espen',
                etternavn: 'Utvikler',
                fnr: '1212121313',
                harRettPForeldrepenger: true,
                kanIkkeOppgis: false,
            },
        },
    } as ForeldrepengesknadContextState,
    skerinfo: skerinfoMorSker,
};

