import React from 'react';
import { Story } from '@storybook/react';
import MockAdapter from 'axios-mock-adapter/types';

import skerinfo from './testdata/skerinfo.json';
import context from './testdata/context.json';
import { SkerinfoDTO } from 'app/types/SkerinfoDTO';
import { ForeldrepengesknadContextState } from 'app/context/ForeldrepengesknadContextConfig';
import ManglendeVedlegg from 'app/steps/manglende-vedlegg/ManglendeVedlegg';
import withIntlProvider from '../../../decorators/withIntl';
import withRouter from '../../../decorators/withRouter';
import withForeldrepengersknadContext from '../../../decorators/withForeldrepengersknadContext';
import ForeldrepengerStateMock from '../../../utils/ForeldrepengerStateMock';
import { lagSendSenereDokument } from 'app/utils/vedleggUtils';
import { AttachmentType } from 'app/types/AttachmentType';
import { Skjemanummer } from 'app/types/Skjemanummer';
import AxiosMock from '../../../utils/AxiosMock';

export default {
    title: 'steps/ManglendeVedlegg',
    component: ManglendeVedlegg,
    decorators: [withRouter, withIntlProvider, withForeldrepengersknadContext],
};

interface Props {
    context: ForeldrepengesknadContextState;
    skerinfo: SkerinfoDTO;
}

const Template: Story<Props> = ({ context, skerinfo }) => {
    const restMock = (apiMock: MockAdapter) => {
        apiMock.onPost('/storage/vedlegg').reply(
            200,
            { data: {} },
            {
                location: '',
            }
        );
    };
    return (
        <AxiosMock mock={restMock}>
            <ForeldrepengerStateMock sknad={context} skerinfo={skerinfo}>
                <ManglendeVedlegg />
            </ForeldrepengerStateMock>
        </AxiosMock>
    );
};

export const Default = Template.bind({});;
Default.args = {
    context: {
        ...context,
        sknad: {
            ...context.sknad,
            barn: {
                ...context.sknad.barn,
                dokumentasjonAvAleneomsorg: [
                    lagSendSenereDokument(AttachmentType.ALENEOMSORG, Skjemanummer.DOK_AV_ALENEOMSORG),
                ],
                terminbekreftelse: [
                    lagSendSenereDokument(AttachmentType.TERMINBEKREFTELSE, Skjemanummer.TERMINBEKREFTELSE),
                ],
            },
        },
    } as ForeldrepengesknadContextState,
    skerinfo,
};

export const ManglerVedleggForUlikePerioder = Template.bind({});
ManglerVedleggForUlikePerioder.args = {
    context: {
        ...context,
        sknad: {
            ...context.sknad,
            sker: {
                ...context.sknad.sker,
                andreInntekterSiste10Mnd: [
                    {
                        pgende: true,
                        tidsperiode: {
                            fom: '2021-08-01',
                            tom: '',
                        },
                        type: 'ETTERLNN_SLUTTPAKKE',
                        vedlegg: [
                            lagSendSenereDokument(
                                AttachmentType.ANNEN_INNTEKT,
                                Skjemanummer.ETTERLNN_ELLER_SLUTTVEDERLAG
                            ),
                        ],
                    },
                    {
                        pgende: true,
                        tidsperiode: {
                            fom: '2021-09-01',
                            tom: '',
                        },
                        type: 'ETTERLNN_SLUTTPAKKE',
                        vedlegg: [
                            lagSendSenereDokument(
                                AttachmentType.ANNEN_INNTEKT,
                                Skjemanummer.ETTERLNN_ELLER_SLUTTVEDERLAG
                            ),
                        ],
                    },
                ],
            },
        },
    } as ForeldrepengesknadContextState,
    skerinfo,
};



