import AnnenForelder, { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import Barn, { isAdoptertAnnetBarn, isAdoptertStebarn, isFdtBarn, isUfdtBarn } from 'app/context/types/Barn';
import Skersituasjon from 'app/context/types/Skersituasjon';
import { Dekningsgrad } from 'app/types/Dekningsgrad';
import { getFamiliehendelsedato } from 'app/utils/barnUtils';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';
import { TilgjengeligeStnadskontoerParams } from './api';

const getFarHarRett = (erFarMedmor: boolean, annenForelder: AnnenForelder) => {
    if (erFarMedmor) {
        return true;
    }

    if (isAnnenForelderOppgitt(annenForelder)) {
        return !!annenForelder.harRettPForeldrepenger;
    }

    return false;
};

const getMorHarRett = (erFarMedmor: boolean, annenForelder: AnnenForelder) => {
    if (!erFarMedmor) {
        return true;
    }

    if (isAnnenForelderOppgitt(annenForelder)) {
        return !!annenForelder.harRettPForeldrepenger;
    }

    return false;
};

const getStnadskontoParams = (
    dekningsgrad: Dekningsgrad,
    barn: Barn,
    annenForelder: AnnenForelder,
    skersituasjon: Skersituasjon
): TilgjengeligeStnadskontoerParams => {
    const erFarMedmor = isFarEllerMedmor(skersituasjon.rolle);

    return {
        antallBarn: barn.antallBarn,
        startdatoUttak: getFamiliehendelsedato(barn),
        dekningsgrad: dekningsgrad,
        farHarRett: getFarHarRett(erFarMedmor, annenForelder),
        morHarRett: getMorHarRett(erFarMedmor, annenForelder),
        morHarAleneomsorg: !erFarMedmor && barn.datoForAleneomsorg !== undefined,
        farHarAleneomsorg: erFarMedmor && barn.datoForAleneomsorg !== undefined,
        fdselsdato: isFdtBarn(barn) ? barn.fdselsdatoer[0] : undefined,
        omsorgsovertakelsesdato: isAdoptertAnnetBarn(barn) || isAdoptertStebarn(barn) ? barn.adopsjonsdato : undefined,
        termindato: isFdtBarn(barn) || isUfdtBarn(barn) ? barn.termindato : undefined,
    };
};

export default getStnadskontoParams;

