import { intlUtils } from '@navikt/fp-common';
import { IntlShape } from 'react-intl';
import { isFdselsnummerFormatValid, isSixteenOrOlder } from './validation/fdselsnummer';

export const validateFdselsnummer =
    (intl: IntlShape, skersFdselsnummer: string, erUtenlandskFnr?: boolean) =>
    (fnr: string): string | undefined => {
        if (erUtenlandskFnr) {
            if (fnr === undefined || fnr === '') {
                return intlUtils(intl, 'valideringsfeil.fdselsnummer.required');
            }

            return undefined;
        }

        if (fnr === skersFdselsnummer) {
            return intlUtils(intl, 'valideringsfeil.fdselsnummer.ugyldigEgetFdselsnummer');
        }

        const validFnrResult = isFdselsnummerFormatValid(fnr);

        if (!erUtenlandskFnr && !isSixteenOrOlder(fnr, validFnrResult) && validFnrResult === 'F') {
            return intlUtils(intl, 'valideringsfeil.fdselsnummer.underSeksten');
        }

        return validFnrResult === 'F' || validFnrResult === 'D'
            ? undefined
            : intlUtils(intl, 'valideringsfeil.fdselsnummer.ugyldigFdselsnummer');
    };
