import { TidsperiodeDate } from '@navikt/fp-common';
import { Attachment } from 'app/types/Attachment';
import { Forelder } from 'app/types/Forelder';
import { MorsAktivitet } from './MorsAktivitet';
import { OppholdrsakType } from './OppholdrsakType';
import { OverfringrsakType } from './OverfringrsakType';
import { PeriodeHullrsak } from './PeriodeHullrsak';
import { PeriodeInfoType } from './PeriodeInfoType';
import { PeriodeResultatType } from './PeriodeResultatType';
import { StnadskontoType } from './StnadskontoType';
import { UtsettelsersakType } from './UtsettelsersakType';

export enum Periodetype {
    Uttak = 'uttak',
    Utsettelse = 'utsettelse',
    Opphold = 'opphold',
    Overfring = 'overfring',
    Hull = 'ubegrunnetOpphold',
    Info = 'info',
}

export enum Arbeidsform {
    arbeidstaker = 'ARBEIDSTAKER',
    frilans = 'FRILANS',
    selvstendignringsdrivende = 'SELVSTENDIG_NRINGSDRIVENDE',
}

export interface PeriodeBase {
    id: string;
    type: Periodetype;
    tidsperiode: TidsperiodeDate;
    vedlegg?: Attachment[];
}

export interface ForeldrepengerFrFdselUttaksperiode extends UttaksperiodeBase {
    konto: StnadskontoType.ForeldrepengerFrFdsel;
    skalIkkeHaUttakFrTermin: boolean;
}

export interface UttaksperiodeBase extends PeriodeBase {
    type: Periodetype.Uttak;
    konto: StnadskontoType;
    forelder: Forelder;
    morsAktivitetIPerioden?: MorsAktivitet;
    nskerSamtidigUttak?: boolean;
    samtidigUttakProsent?: string;
    gradert?: boolean;
    stillingsprosent?: string;
    orgnumre?: string[];
    arbeidsformer?: Arbeidsform[];
    erArbeidstaker?: boolean;
    harIkkeAktivitetskrav?: boolean;
    vedlegg?: Attachment[];
    nskerFlerbarnsdager?: boolean;
    erMorForSyk?: boolean;
}

export type Uttaksperiode = UttaksperiodeBase | ForeldrepengerFrFdselUttaksperiode;

export interface Utsettelsesperiode extends PeriodeBase {
    type: Periodetype.Utsettelse;
    rsak: UtsettelsersakType;
    forelder: Forelder;
    morsAktivitetIPerioden?: MorsAktivitet;
    orgnumre?: string[];
    erArbeidstaker: boolean;
    arbeidsformer?: Arbeidsform[];
    harAvtaleOmFulltidForDeltidsstilling?: boolean;
}

export interface Oppholdsperiode extends PeriodeBase {
    type: Periodetype.Opphold;
    rsak: OppholdrsakType;
    forelder: Forelder;
}

export interface Overfringsperiode extends PeriodeBase {
    type: Periodetype.Overfring;
    konto: StnadskontoType;
    forelder: Forelder;
    rsak: OverfringrsakType;
}

export interface PeriodeHull extends PeriodeBase {
    type: Periodetype.Hull;
    tidsperiode: TidsperiodeDate;
    rsak?: PeriodeHullrsak;
}

interface InfoPeriodeBase extends PeriodeBase {
    type: Periodetype.Info;
    infotype: PeriodeInfoType;
    overskrives: boolean;
    visPeriodeIPlan: boolean;
}

export interface AvslttPeriode extends InfoPeriodeBase {
    type: Periodetype.Info;
    infotype: PeriodeInfoType.avslttPeriode;
    avslttPeriodeType?: Periodetype;
    stnadskonto: StnadskontoType;
    forelder: Forelder;
    overskrives: true;
    visPeriodeIPlan: boolean;
}

export interface UttakAnnenPartInfoPeriode extends InfoPeriodeBase {
    type: Periodetype.Info;
    infotype: PeriodeInfoType.uttakAnnenPart;
    rsak: OppholdrsakType;
    forelder: Forelder;
    overskrives: true;
    resultatType: PeriodeResultatType;
    visPeriodeIPlan: boolean;
    nskerSamtidigUttak?: boolean;
    samtidigUttakProsent?: string;
    gradert?: boolean;
    stillingsprosent?: string;
}

export interface UtsettelseAnnenPartInfoPeriode extends InfoPeriodeBase {
    type: Periodetype.Info;
    infotype: PeriodeInfoType.utsettelseAnnenPart;
    rsak: UtsettelsersakType;
    forelder: Forelder;
    overskrives: true;
    resultatType: PeriodeResultatType;
    visPeriodeIPlan: boolean;
}

export type InfoPeriode = AvslttPeriode | UttakAnnenPartInfoPeriode | UtsettelseAnnenPartInfoPeriode;

export type Periode =
    | Uttaksperiode
    | Utsettelsesperiode
    | Oppholdsperiode
    | Overfringsperiode
    | PeriodeHull
    | InfoPeriode;

export function isUttaksperiode(periode: Periode): periode is Uttaksperiode {
    return periode.type === Periodetype.Uttak;
}

export const isForeldrepengerFrFdselUttaksperiode = (
    periode: Periode
): periode is ForeldrepengerFrFdselUttaksperiode => {
    return periode.type === Periodetype.Uttak && periode.konto === StnadskontoType.ForeldrepengerFrFdsel;
};

export const isUttakAvFellesperiode = (periode: Periode): periode is Uttaksperiode => {
    return periode.type === Periodetype.Uttak && periode.konto === StnadskontoType.Fellesperiode;
};

export const isUtsettelsesperiode = (periode: Periode): periode is Utsettelsesperiode => {
    return periode.type === Periodetype.Utsettelse;
};

export const isUtsettelsePgaFerie = (periode: Periode): periode is Utsettelsesperiode => {
    return isUtsettelsesperiode(periode) && periode.rsak === UtsettelsersakType.Ferie;
};

export const isUtsettelsePgaArbeid = (periode: Periode): periode is Utsettelsesperiode => {
    return isUtsettelsesperiode(periode) && periode.rsak === UtsettelsersakType.Arbeid;
};

export const isOverfringsperiode = (periode: Periode): periode is Overfringsperiode => {
    return periode.type === Periodetype.Overfring;
};

export const isOppholdsperiode = (periode: Periode): periode is Oppholdsperiode => {
    return periode.type === Periodetype.Opphold;
};

export const isInfoPeriode = (periode: Periode): periode is InfoPeriode => {
    return periode.type === Periodetype.Info && periode.overskrives === true;
};

export const isHull = (periode: Periode): periode is PeriodeHull => {
    return periode.type === Periodetype.Hull;
};
