import { hasValue, intlUtils } from '@navikt/fp-common';
import { YesOrNo } from '@navikt/sif-common-formik/lib';
import { isDateInTheFuture, isDateABeforeDateB } from 'app/utils/dateUtils';
import { isISODateString } from 'nav-datovelger';
import { IntlShape } from 'react-intl';

export const validateNavnPOppdragsgiver = (intl: IntlShape) => (navn: string) => {
    if (!hasValue(navn)) {
        return intlUtils(intl, 'valideringsfeil.inntektsinformasjon.frilansoppdrag.navnPOppdragsgiver.pkrevd');
    }

    return undefined;
};

export const validateOppdragFom = (intl: IntlShape, tom: string, oppstartsdato: string) => (fom: string) => {
    if (!hasValue(fom)) {
        return intlUtils(intl, 'valideringsfeil.fraOgMedDato.pkrevd');
    }

    if (!isISODateString(fom)) {
        return intlUtils(intl, 'valideringsfeil.fraOgMedDato.gyldigDato');
    }

    if (isDateInTheFuture(fom)) {
        return intlUtils(intl, 'valideringsfeil.fraOgMedDato.erIFremtiden');
    }

    if (isDateABeforeDateB(fom, oppstartsdato)) {
        return intlUtils(intl, 'valideringsfeil.inntektsinformasjon.frilansoppdrag.fom.frOppstartsdato');
    }

    if (isDateABeforeDateB(tom, fom)) {
        return intlUtils(intl, 'valideringsfeil.fraOgMedDato.frTilDato');
    }

    return undefined;
};

export const validatePgendeOppdrag = (intl: IntlShape) => (pgende: YesOrNo) => {
    if (pgende === YesOrNo.UNANSWERED) {
        return intlUtils(intl, 'valideringsfeil.inntektsinformasjon.frilansoppdrag.pgende.pkrevd');
    }

    return undefined;
};

export const validateOppdragTom =
    (intl: IntlShape, pgende: YesOrNo, fom: string, oppstartsdato: string) => (tom: string) => {
        if (pgende === YesOrNo.YES) {
            return undefined;
        }

        if (!hasValue(tom)) {
            return intlUtils(intl, 'valideringsfeil.tilOgMedDato.pkrevd');
        }

        if (!isISODateString(tom)) {
            return intlUtils(intl, 'valideringsfeil.tilOgMedDato.gyldigDato');
        }

        if (isDateInTheFuture(tom)) {
            return intlUtils(intl, 'valideringsfeil.tilOgMedDato.erIFremtiden');
        }

        if (isDateABeforeDateB(tom, oppstartsdato)) {
            return intlUtils(intl, 'valideringsfeil.inntektsinformasjon.frilansoppdrag.tom.frOppstartsdato');
        }

        if (isDateABeforeDateB(tom, fom)) {
            return intlUtils(intl, 'valideringsfeil.tilOgMedDato.etterFraDato');
        }

        return undefined;
    };
