import { Kjnn } from '@navikt/fp-common';
import AnnenForelder, { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import { NavnPForeldre } from 'app/types/NavnPForeldre';
import Person from 'app/types/Person';

export const formaterNavn = (fornavn: string, etternavn: string, mellomnavn?: string) => {
    return mellomnavn ? `${fornavn} ${mellomnavn} ${etternavn}` : `${fornavn} ${etternavn}`;
};

const navnSlutterPSLyd = (navn: string): boolean => {
    const sisteBokstav = navn.charAt(navn.length - 1).toLowerCase();
    return sisteBokstav === 's' || sisteBokstav === 'x' || sisteBokstav === 'z';
};

export const getNavnGenitivEierform = (navn: string, locale: string): string => {
    if (locale !== 'nb') {
        return navn;
    }
    if (navnSlutterPSLyd(navn)) {
        return `${navn}'`;
    }
    return `${navn}s`;
};

export const getKjnnFromFnr = (annenForelder: AnnenForelder): Kjnn | undefined => {
    if (isAnnenForelderOppgitt(annenForelder)) {
        const { fnr } = annenForelder;

        if (fnr.length !== 11) {
            return undefined;
        }
        return parseInt(fnr.charAt(8), 10) % 2 === 0 ? 'K' : 'M';
    }

    return undefined;
};

export const getMorErAleneOmOmsorg = (
    skerErMor: boolean,
    skerErAleneOmOmsorg: boolean,
    annenForelder: AnnenForelder
) => {
    return skerErMor && (skerErAleneOmOmsorg || annenForelder.kanIkkeOppgis === true);
};

export const getFarMedmorErAleneOmOmsorg = (
    skerErFarMedmor: boolean,
    skerErAleneOmOmsorg: boolean,
    annenForelder: AnnenForelder
) => {
    return skerErFarMedmor && (skerErAleneOmOmsorg || annenForelder.kanIkkeOppgis === true);
};

export const getNavnPForeldre = (
    person: Person,
    annenForelder: AnnenForelder,
    erFarEllerMedmor: boolean
): NavnPForeldre => {
    const navnSker = person.fornavn;
    const navnAnnenForelder = isAnnenForelderOppgitt(annenForelder) ? annenForelder.fornavn : '';
    const navnMor = erFarEllerMedmor ? navnAnnenForelder : navnSker;
    const navnFarMedmor = erFarEllerMedmor ? navnSker : navnAnnenForelder;

    return {
        mor: navnMor,
        farMedmor: navnFarMedmor,
    };
};
