import { Block, intlUtils } from '@navikt/fp-common';
import { ISOStringToDate } from '@navikt/sif-common-formik/lib';
import actionCreator from 'app/context/action/actionCreator';
import { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import { FarMedmorAleneomsorgFdselUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import SknadRoutes from 'app/routes/routes';
import { Forelder } from 'app/types/Forelder';
import { TilgjengeligeStnadskontoerDTO } from 'app/types/TilgjengeligeStnadskontoerDTO';
import { getErMorUfr } from 'app/utils/annenForelderUtils';
import { getFamiliehendelsedato } from 'app/utils/barnUtils';
import { formaterDatoUtenDag } from 'app/utils/dateUtils';
import { getDekningsgradFromString } from 'app/utils/getDekningsgradFromString';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import useSknad from 'app/utils/hooks/useSknad';
import useUttaksplanInfo from 'app/utils/hooks/useUttaksplanInfo';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';
import { getValgtStnadskontoFor80Og100Prosent } from 'app/utils/stnadskontoUtils';
import { Hovedknapp } from 'nav-frontend-knapper';
import React, { FunctionComponent } from 'react';
import { useIntl } from 'react-intl';
import TilgjengeligeDagerGraf from '../../tilgjengeligeDagerGraf/TilgjengeligeDagerGraf';
import { getTilgjengeligeDager } from '../../tilgjengeligeDagerGraf/tilgjengeligeDagerUtils';
import DekningsgradSprsml from '../sprsml/DekningsgradSprsml';
import {
    FarMedmorAleneomsorgFdselFormComponents,
    FarMedmorAleneomsorgFdselFormData,
    FarMedmorAleneomsorgFdselFormField,
} from './farMedmorAleneomsorgFdselFormConfig';
import farMedmorAleneomsorgFdselAdopsjonQuestionsConfig from './farMedmorAleneomsorgFdselQuestionsConfig';
import {
    getInitialFarMedmorAleneomsorgFdselValues,
    mapFarMedmorAleneomsorgFdselFormToState,
} from './farMedmorAleneomsorgFdselUtils';
import { validateStartdatoUttakFarMedmorAleneomsorgFdsel } from './validation/farMedmorAleneomsorgFdselValidation';

interface Props {
    tilgjengeligeStnadskontoer100DTO: TilgjengeligeStnadskontoerDTO;
    tilgjengeligeStnadskontoer80DTO: TilgjengeligeStnadskontoerDTO;
}

const FarMedmorAleneomsorgFdsel: FunctionComponent<Props> = ({
    tilgjengeligeStnadskontoer100DTO,
    tilgjengeligeStnadskontoer80DTO,
}) => {
    const { skersituasjon, sker, annenForelder, barn, dekningsgrad } = useSknad();
    const { person } = useSkerinfo();
    const erFarEllerMedmor = isFarEllerMedmor(skersituasjon.rolle);
    const familiehendelsesdato = getFamiliehendelsedato(barn);
    const intl = useIntl();

    const erFdsel = skersituasjon.situasjon === 'fdsel';
    const lagretUttaksplanInfo = useUttaksplanInfo<FarMedmorAleneomsorgFdselUttaksplanInfo>();

    const onValidSubmitHandler = (values: Partial<FarMedmorAleneomsorgFdselFormData>) => {
        const uttaksplanInfo: FarMedmorAleneomsorgFdselUttaksplanInfo = mapFarMedmorAleneomsorgFdselFormToState(
            values,
            familiehendelsesdato
        );
        return [
            actionCreator.setUttaksplanInfo(uttaksplanInfo),
            actionCreator.setDekningsgrad(getDekningsgradFromString(values.dekningsgrad)),
        ];
    };

    const onValidSubmit = useOnValidSubmit(onValidSubmitHandler, SknadRoutes.UTTAKSPLAN);

    const shouldRender = erFdsel && erFarEllerMedmor && (!!sker.erAleneOmOmsorg || annenForelder.kanIkkeOppgis);

    if (!shouldRender) {
        return null;
    }

    const navnFar = erFarEllerMedmor
        ? person.fornavn
        : isAnnenForelderOppgitt(annenForelder)
        ? annenForelder.fornavn
        : '';

    const erMorUfr = getErMorUfr(annenForelder, erFarEllerMedmor);
    const tilgjengeligeStnadskontoer = getValgtStnadskontoFor80Og100Prosent(
        tilgjengeligeStnadskontoer80DTO,
        tilgjengeligeStnadskontoer100DTO,
        familiehendelsesdato,
        erMorUfr
    );

    return (
        <FarMedmorAleneomsorgFdselFormComponents.FormikWrapper
            initialValues={getInitialFarMedmorAleneomsorgFdselValues(
                lagretUttaksplanInfo,
                familiehendelsesdato,
                dekningsgrad
            )}
            onSubmit={onValidSubmit}
            renderForm={({ values: formValues }) => {
                const visibility = farMedmorAleneomsorgFdselAdopsjonQuestionsConfig.getVisbility(formValues);

                const valgtStnadskonto = tilgjengeligeStnadskontoer[formValues.dekningsgrad];

                return (
                    <FarMedmorAleneomsorgFdselFormComponents.Form
                        includeButtons={false}
                        includeValidationSummary={true}
                    >
                        <Block
                            padBottom="xxl"
                            visible={visibility.isVisible(FarMedmorAleneomsorgFdselFormField.dekningsgrad)}
                        >
                            <DekningsgradSprsml
                                FormKomponent={FarMedmorAleneomsorgFdselFormComponents}
                                dekningsgradFeltNavn={FarMedmorAleneomsorgFdselFormField.dekningsgrad}
                                tilgjengeligeStnadskontoer={tilgjengeligeStnadskontoer}
                            />
                        </Block>
                        <Block visible={formValues.dekningsgrad !== ''}>
                            {valgtStnadskonto && (
                                <TilgjengeligeDagerGraf
                                    erDeltUttak={false}
                                    erFarEllerMedmor={true}
                                    navnFarMedmor={navnFar}
                                    navnMor=""
                                    tilgjengeligeDager={getTilgjengeligeDager(
                                        valgtStnadskonto,
                                        false,
                                        Forelder.farMedmor
                                    )}
                                />
                            )}
                        </Block>
                        <Block
                            padBottom="l"
                            visible={visibility.isVisible(
                                FarMedmorAleneomsorgFdselFormField.startPOmsorgsovertakelse
                            )}
                        >
                            <FarMedmorAleneomsorgFdselFormComponents.YesOrNoQuestion
                                name={FarMedmorAleneomsorgFdselFormField.startPOmsorgsovertakelse}
                                legend={intlUtils(intl, 'uttaksplaninfo.startdatoAleneomsorgFarMedmor.sprsml')}
                                labels={{
                                    yes: intlUtils(
                                        intl,
                                        'uttaksplaninfo.startdatoAdopsjon.alternativ.omsorgsovertakelse',
                                        {
                                            dato: formaterDatoUtenDag(ISOStringToDate(familiehendelsesdato)!),
                                        }
                                    ),
                                    no: intlUtils(intl, 'uttaksplaninfo.startdatoAleneomsorgFarMedmor.annenDato'),
                                }}
                            />
                        </Block>
                        <Block
                            padBottom="l"
                            visible={visibility.isVisible(FarMedmorAleneomsorgFdselFormField.startdatoUttak)}
                        >
                            <FarMedmorAleneomsorgFdselFormComponents.DatePicker
                                name={FarMedmorAleneomsorgFdselFormField.startdatoUttak}
                                label="Startdato"
                                validate={validateStartdatoUttakFarMedmorAleneomsorgFdsel(intl, familiehendelsesdato)}
                                minDate={ISOStringToDate(familiehendelsesdato)}
                            />
                        </Block>
                        <Block visible={visibility.areAllQuestionsAnswered()} textAlignCenter={true}>
                            <Hovedknapp>{intlUtils(intl, 'sknad.gVidere')}</Hovedknapp>
                        </Block>
                    </FarMedmorAleneomsorgFdselFormComponents.Form>
                );
            }}
        />
    );
};

export default FarMedmorAleneomsorgFdsel;
