import React, { FunctionComponent } from 'react';
import dayjs from 'dayjs';
import { FormattedMessage, useIntl } from 'react-intl';
import { Block, intlUtils } from '@navikt/fp-common';
import Veilederpanel from 'nav-frontend-veilederpanel';
import { ISOStringToDate } from '@navikt/sif-common-formik/lib';
import VeilederNormal from 'app/assets/VeilederNormal';
import useSknad from 'app/utils/hooks/useSknad';
import isFarEllerMedmor from 'app/utils/isFarEllerMedmor';
import actionCreator from 'app/context/action/actionCreator';
import { getFamiliehendelsedato } from 'app/utils/barnUtils';
import { getValgtStnadskontoFor80Og100Prosent } from 'app/utils/stnadskontoUtils';
import { isAnnenForelderOppgitt } from 'app/context/types/AnnenForelder';
import { TilgjengeligeStnadskontoerDTO } from 'app/types/TilgjengeligeStnadskontoerDTO';
import { isFdtBarn } from 'app/context/types/Barn';
import useSkerinfo from 'app/utils/hooks/useSkerinfo';
import { formaterNavn } from 'app/utils/personUtils';
import { getFlerbarnsuker } from '../../../utils/uttaksplanHarForMangeFlerbarnsuker';
import { MorFdselFormComponents, MorFdselFormData, MorFdselFormField } from './morFdselFormConfig';
import { getTilgjengeligeDager } from '../../tilgjengeligeDagerGraf/tilgjengeligeDagerUtils';
import TilgjengeligeDagerGraf from '../../tilgjengeligeDagerGraf/TilgjengeligeDagerGraf';
import { Tidsperioden } from '../../../utils/Tidsperioden';
import { getInitialMorFdselValues, mapMorFdselFormToState } from './morFdselUtils';
import StartdatoPermisjonMor from './StartdatoPermisjonMor';
import FordelingFellesperiodeSprsml from '../../fordelingFellesperiode/FordelingFellesperiodeSprsml';
import { Uttaksdagen } from 'app/steps/uttaksplan-info/utils/Uttaksdagen';
import uttaksConstants from 'app/constants';
import { Forelder } from 'app/types/Forelder';
import useOnValidSubmit from 'app/utils/hooks/useOnValidSubmit';
import SknadRoutes from 'app/routes/routes';
import { MorFdselUttaksplanInfo } from 'app/context/types/UttaksplanInfo';
import { Hovedknapp } from 'nav-frontend-knapper';
import { morFdselQuestionsConfig } from './morFdselQuestionsConfig';
import useUttaksplanInfo from 'app/utils/hooks/useUttaksplanInfo';
import DekningsgradSprsml from '../sprsml/DekningsgradSprsml';
import { getDekningsgradFromString } from 'app/utils/getDekningsgradFromString';

const skalViseInfoOmPrematuruker = (fdselsdato: Date | undefined, termindato: Date | undefined): boolean => {
    if (fdselsdato === undefined || termindato === undefined) {
        return false;
    }

    const fdselsdatoEtterEllerLikFrsteJuli = dayjs(fdselsdato).isSameOrAfter(dayjs(new Date('2019-07-01')));

    return (
        dayjs(fdselsdato).add(7, 'weeks').add(3, 'days').isBefore(dayjs(termindato)) &&
        fdselsdatoEtterEllerLikFrsteJuli
    );
};

interface Props {
    tilgjengeligeStnadskontoer100DTO: TilgjengeligeStnadskontoerDTO;
    tilgjengeligeStnadskontoer80DTO: TilgjengeligeStnadskontoerDTO;
}

const MorFdsel: FunctionComponent<Props> = ({
    tilgjengeligeStnadskontoer100DTO,
    tilgjengeligeStnadskontoer80DTO,
}) => {
    const intl = useIntl();
    const {
        annenForelder,
        skersituasjon,
        barn,
        sker: { erAleneOmOmsorg },
        dekningsgrad,
    } = useSknad();
    const {
        person: { fornavn, mellomnavn, etternavn },
    } = useSkerinfo();
    const lagretUttaksplanInfo = useUttaksplanInfo<MorFdselUttaksplanInfo>();

    const erMor = !isFarEllerMedmor(skersituasjon.rolle);
    const erFdsel = skersituasjon.situasjon === 'fdsel';

    const shouldRender = erMor && erFdsel;

    const onValidSubmitHandler = (values: Partial<MorFdselFormData>) => {
        return [
            actionCreator.setUttaksplanInfo(mapMorFdselFormToState(values)),
            actionCreator.setDekningsgrad(getDekningsgradFromString(values.dekningsgrad)),
        ];
    };

    const onValidSubmit = useOnValidSubmit(onValidSubmitHandler, SknadRoutes.UTTAKSPLAN);

    if (!shouldRender) {
        return null;
    }

    const antallBarn = parseInt(barn.antallBarn, 10);

    const fdselsdato = isFdtBarn(barn) ? ISOStringToDate(barn.fdselsdatoer[0]) : undefined;
    const termindato = isFdtBarn(barn) ? ISOStringToDate(barn.termindato) : undefined;
    const visInfoOmPrematuruker =
        skersituasjon.situasjon === 'fdsel' ? skalViseInfoOmPrematuruker(fdselsdato, termindato) : false;
    const ekstraDagerGrunnetPrematurFdsel = visInfoOmPrematuruker
        ? Tidsperioden({ fom: fdselsdato!, tom: termindato! }).getAntallUttaksdager() - 1
        : undefined;

    const oppgittAnnenForelder = isAnnenForelderOppgitt(annenForelder) ? annenForelder : undefined;
    const erMorUfr = !!oppgittAnnenForelder?.erUfr;
    const annenForelderFornavn = oppgittAnnenForelder?.fornavn;
    const harRettPForeldrepenger = !!oppgittAnnenForelder?.harRettPForeldrepenger;
    const navnFarMedmor = oppgittAnnenForelder
        ? formaterNavn(oppgittAnnenForelder.fornavn, oppgittAnnenForelder.etternavn)
        : '';

    const navnMor = formaterNavn(fornavn, etternavn, mellomnavn);
    const familiehendelsesdato = getFamiliehendelsedato(barn);
    const frsteUttaksdag = Uttaksdagen(ISOStringToDate(familiehendelsesdato)!).denneEllerNeste();
    const defaultPermisjonStartdato = Uttaksdagen(frsteUttaksdag).trekkFra(
        uttaksConstants.ANTALL_UKER_FORELDREPENGER_FR_FDSEL * 5
    );

    const erDeltUttak = isAnnenForelderOppgitt(annenForelder) ? !!annenForelder.harRettPForeldrepenger : false;

    const tilgjengeligeStnadskontoer = getValgtStnadskontoFor80Og100Prosent(
        tilgjengeligeStnadskontoer80DTO,
        tilgjengeligeStnadskontoer100DTO,
        familiehendelsesdato,
        erMorUfr
    );

    return (
        <MorFdselFormComponents.FormikWrapper
            initialValues={getInitialMorFdselValues(defaultPermisjonStartdato, lagretUttaksplanInfo, dekningsgrad)}
            onSubmit={onValidSubmit}
            renderForm={({ values: formValues, setFieldValue }) => {
                const visibility = morFdselQuestionsConfig.getVisbility({
                    ...formValues,
                    harRettPForeldrepenger,
                    erAleneOmOmsorg,
                });

                const valgtStnadskonto = tilgjengeligeStnadskontoer[formValues.dekningsgrad];

                return (
                    <MorFdselFormComponents.Form includeButtons={false} includeValidationSummary={true}>
                        <Block padBottom="l">
                            <DekningsgradSprsml
                                FormKomponent={MorFdselFormComponents}
                                dekningsgradFeltNavn={MorFdselFormField.dekningsgrad}
                                tilgjengeligeStnadskontoer={tilgjengeligeStnadskontoer}
                            />
                        </Block>
                        <Block padBottom="l" visible={visibility.isAnswered(MorFdselFormField.dekningsgrad)}>
                            {valgtStnadskonto && (
                                <TilgjengeligeDagerGraf
                                    erDeltUttak={erDeltUttak}
                                    erFarEllerMedmor={false}
                                    navnFarMedmor={navnFarMedmor}
                                    navnMor={navnMor}
                                    tilgjengeligeDager={getTilgjengeligeDager(
                                        valgtStnadskonto,
                                        false,
                                        Forelder.farMedmor
                                    )}
                                />
                            )}
                        </Block>
                        <Block padBottom="l" visible={visInfoOmPrematuruker === true}>
                            <Veilederpanel fargetema="normal" svg={<VeilederNormal transparentBackground={true} />}>
                                <FormattedMessage
                                    id="uttaksplaninfo.veileder.informasjonPrematuruker"
                                    values={{
                                        antallprematuruker: Math.floor(ekstraDagerGrunnetPrematurFdsel! / 5),
                                        antallprematurdager: ekstraDagerGrunnetPrematurFdsel! % 5,
                                    }}
                                />
                            </Veilederpanel>
                        </Block>
                        <Block visible={visibility.isAnswered(MorFdselFormField.dekningsgrad)}>
                            <StartdatoPermisjonMor
                                permisjonStartdato={formValues.permisjonStartdato}
                                skalIkkeHaUttakFrTermin={formValues.skalIkkeHaUttakFrTermin}
                            />
                        </Block>
                        <Block
                            visible={
                                erAleneOmOmsorg === false &&
                                harRettPForeldrepenger &&
                                visibility.isAnswered(MorFdselFormField.dekningsgrad)
                            }
                        >
                            <Block
                                padBottom="l"
                                visible={
                                    antallBarn > 1 &&
                                    (formValues.permisjonStartdato !== undefined ||
                                        formValues.skalIkkeHaUttakFrTermin === true)
                                }
                            >
                                <Veilederpanel fargetema="normal" svg={<VeilederNormal transparentBackground={true} />}>
                                    <FormattedMessage
                                        id="uttaksplaninfo.veileder.flerbarnsInformasjon"
                                        values={{
                                            uker: getFlerbarnsuker(formValues.dekningsgrad!, antallBarn),
                                            navnFar: navnFarMedmor,
                                            navnMor: navnMor,
                                        }}
                                    />
                                </Veilederpanel>
                            </Block>
                            <Block
                                padBottom="l"
                                visible={visibility.isVisible(MorFdselFormField.fellesperiodeukerMor)}
                            >
                                <FordelingFellesperiodeSprsml
                                    setFieldValue={setFieldValue}
                                    valgtStnadskonto={valgtStnadskonto}
                                    valgtFellesperiodeukerMor={formValues.fellesperiodeukerMor}
                                    mor={navnMor}
                                    farMedmor={navnFarMedmor}
                                    annenForelderErFarEllerMedmor={navnFarMedmor === annenForelderFornavn}
                                />
                            </Block>
                        </Block>
                        <Block visible={visibility.areAllQuestionsAnswered()} textAlignCenter={true}>
                            <Hovedknapp>{intlUtils(intl, 'sknad.gVidere')}</Hovedknapp>
                        </Block>
                    </MorFdselFormComponents.Form>
                );
            }}
        />
    );
};

export default MorFdsel;
