import { Dekningsgrad } from 'app/types/Dekningsgrad';
import { TilgjengeligeStnadskontoerDTO } from 'app/types/TilgjengeligeStnadskontoerDTO';
import { TilgjengeligStnadskonto } from 'app/types/TilgjengeligStnadskonto';
import dayjs from 'dayjs';
import { StnadskontoType } from 'uttaksplan/types/StnadskontoType';

const getAktivitetsFrieUkerForeldrepenger = (dekningsgrad: Dekningsgrad, startdatoUttak: string): number => {
    if (dekningsgrad === Dekningsgrad.HUNDRE_PROSENT) {
        return 15;
    } else {
        return dayjs(startdatoUttak).isBefore(dayjs(new Date(2019, 0, 1))) ? 15 : 19;
    }
};

const opprettAktivitetsFriKonto = (
    kontoer: TilgjengeligStnadskonto[],
    dekningsgrad: Dekningsgrad,
    startdatoUttak: string,
    erMorUfr: boolean
): TilgjengeligStnadskonto[] => {
    if (erMorUfr === false) {
        return kontoer;
    }

    const nyeKontoer: TilgjengeligStnadskonto[] = [];
    const aktivitetskravFrieDagerForeldrepenger = getAktivitetsFrieUkerForeldrepenger(dekningsgrad, startdatoUttak) * 5;

    nyeKontoer.push({ ...kontoer[0], dager: kontoer[0].dager - aktivitetskravFrieDagerForeldrepenger });
    nyeKontoer.push({ konto: StnadskontoType.AktivitetsfriKvote, dager: aktivitetskravFrieDagerForeldrepenger });

    return nyeKontoer;
};

const mapTilgjengeligStnadskontoDTOToTilgjengeligStnadskonto = (
    stnadskontoerDTO: TilgjengeligeStnadskontoerDTO,
    dekningsgrad: Dekningsgrad,
    startdatoUttak: string,
    erMorUfr: boolean
): TilgjengeligStnadskonto[] => {
    let tilgjengeligeStnadskontoer: TilgjengeligStnadskonto[] = [];

    Object.keys(stnadskontoerDTO.kontoer)
        .filter((konto: string) => konto !== StnadskontoType.Flerbarnsdager)
        .forEach((konto) => {
            tilgjengeligeStnadskontoer.push({
                konto: konto as StnadskontoType,
                dager: stnadskontoerDTO.kontoer[konto],
            });
        });

    tilgjengeligeStnadskontoer = opprettAktivitetsFriKonto(
        tilgjengeligeStnadskontoer,
        dekningsgrad,
        startdatoUttak,
        erMorUfr
    );

    return tilgjengeligeStnadskontoer;
};

export const getValgtStnadskontoFor80Og100Prosent = (
    kontoer80: TilgjengeligeStnadskontoerDTO,
    kontoer100: TilgjengeligeStnadskontoerDTO,
    familiehendelsesdato: string,
    erMorUfr: boolean
) => {
    const ttiProsent = mapTilgjengeligStnadskontoDTOToTilgjengeligStnadskonto(
        kontoer80,
        Dekningsgrad.TTI_PROSENT,
        familiehendelsesdato,
        erMorUfr
    );
    const hundreProsent = mapTilgjengeligStnadskontoDTOToTilgjengeligStnadskonto(
        kontoer100,
        Dekningsgrad.HUNDRE_PROSENT,
        familiehendelsesdato,
        erMorUfr
    );
    return {
        [Dekningsgrad.TTI_PROSENT]: ttiProsent,
        [Dekningsgrad.HUNDRE_PROSENT]: hundreProsent,
    };
};
