import Barn, { isFdtBarn, isUfdtBarn } from 'app/context/types/Barn';
import { RegistrertBarn } from 'app/types/Person';
import dayjs from 'dayjs';

export const getFamiliehendelsedato = (barn: Barn): string => {
    if (isFdtBarn(barn)) {
        return barn.fdselsdatoer[0];
    }
    if (isUfdtBarn(barn)) {
        return barn.termindato;
    }

    return barn.adopsjonsdato;
};

export const getRegistrertBarnOmDetFinnes = (
    barn: Barn,
    registrerteBarn: RegistrertBarn[]
): RegistrertBarn | undefined => {
    return registrerteBarn.length > 0 && isFdtBarn(barn)
        ? registrerteBarn.find((regBarn) => dayjs(regBarn.fdselsdato).isSame(barn.fdselsdatoer[0]))
        : undefined;
};
